import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import ScanVideo from '../../Assets/scan_video.mp4'
import HighVoltage from '../../Assets/high_voltage.png'
class ResistivityAuto extends Component {

  constructor(props) {

    super(props)
    this.interval = null
    this.warningInterval = null
    this.calibration = 0
    this.state = {
      A: 0,
      B: 0,
      C: 0,
      D: 0,
      E: 0,
      F: 0
    }
  }

  componentWillUnmount() {
    clearInterval(this.interval)

  }

  componentDidMount() {
    this.calibration = localStorage.getItem("resCalibration")
    console.log(this.calibration)

    this.interval = setInterval(() => {
      console.log(this.refs.ScanVideo.currentTime)
      switch (Math.floor(this.refs.ScanVideo.currentTime)) {
        case 3:
          this.getResData('A')
          break
        case 7:
          this.getResData('B')
          break
        case 11:
          this.getResData('C')
          break
        case 14:
          this.getResData('D')
          break
        case 18:
          this.getResData('E')
          break
        case 22:
          this.getResData('F')
          break
        case 25:
          this.getResData('Z')
          clearInterval(this.interval)
          break
        default:
          break
      }
    }, 300);


    setTimeout(() => {
      this.refs.ResistivityAuto.style.opacity = "1"
      this.refs.ResistivityAuto.style.transform = "scale(1)"
    }, 100)
  }

  calculateAfterCalibration(value, calibration){
    let result =  parseInt(value) + parseInt(calibration)
    if(result < 20){
      result = 20
    }
    else if(result > 1020){
      result = 1020
    }
    return result
  }

  getResData = async (prop) => {
    let sensorResponse = await this.refs.socket.requestScan(prop)
    if (sensorResponse.type = "scan") {
      if (sensorResponse.line == 'A') {
        if (this.state.A === 0) {
          this.setState({ A: this.calculateAfterCalibration(sensorResponse.payload, this.calibration) })
          console.log(sensorResponse)
        }
      }
      if (sensorResponse.line == 'B') {
        if (this.state.B === 0) {
          this.setState({ B: this.calculateAfterCalibration(sensorResponse.payload, this.calibration) })
          console.log(sensorResponse)
        }
      }
      if (sensorResponse.line == 'C') {
        if (this.state.C === 0) {
          this.setState({ C: this.calculateAfterCalibration(sensorResponse.payload, this.calibration) })
          console.log(sensorResponse)
        }
      }
      if (sensorResponse.line == 'D') {
        if (this.state.D === 0) {
          this.setState({ D: this.calculateAfterCalibration(sensorResponse.payload, this.calibration) })
          console.log(sensorResponse)
        }
      }
      if (sensorResponse.line == 'E') {
        if (this.state.E === 0) {
          this.setState({ E: this.calculateAfterCalibration(sensorResponse.payload, this.calibration) })
          console.log(sensorResponse)
        }
      }
      if (sensorResponse.line == 'F') {
        if (this.state.F === 0) {
          this.setState({ F: this.calculateAfterCalibration(sensorResponse.payload, this.calibration) })
          console.log(sensorResponse)
        }
      }
    }

  }

  closeProps = async () => {
    let sensorResponse = await this.refs.socket.requestScan('Z')
  }


  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'ok':
        console.log("ok")
        //Tarama Başlatılıyor...
        this.refs.ScanVideoWrapper.style.display = "block"


          console.log("video started")
          this.refs.ScanVideo.play()


        break
      case 'back':
        //eğer video açıksa videoyu kapatıyor. - değilse ana menüye dönüyor.
        if (this.refs.ScanVideoWrapper.style.display === "block") {
          this.refs.ScanVideo.currentTime = 0
          this.refs.ScanVideo.pause()
          this.closeProps()
          setTimeout(() => {
            this.closeProps()
          }, 1500);
          this.refs.ScanVideoWrapper.style.display = "none"
        } else {
          this.closeProps()
          this.props.navigateTo("menuScreen")
        }
        return
      default:
        break
    }
  }



  _onEnded = () => {
    localStorage.setItem("resJustScanned", "true")
    console.log("video has finished: Scan video")
    this.props.navigateTo("resistivityAutoResultScreen")
    this.props.getResistivityData(this.state)
  }

  render() {
    return (
      <div className="resistivity-auto" ref="ResistivityAuto">
        <div>
          <div className="resistivity-intro-video" ref="startResPage"></div>
          <div className="resistivity-intro-text"> <i className="fas fa-bolt"></i> {this.props.strings["res_warning"]}</div>
          <div className="start-button"> {this.props.strings["res_start"]}</div>
        </div>
        <div className="resistivity-auto-scan-video" style={{ display: "none" }} ref="ScanVideoWrapper">
          <video className="res-video" muted preload="auto" src="http://localhost:8080/videos/out.mp4" ref="ScanVideo" onEnded={this._onEnded} />
        </div>

        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default ResistivityAuto