import React, { Component } from 'react'
import axios from 'axios'
import './FileBrowser.css'
import SocketHelper from '../../SocketHelper'
import Moment from 'react-moment';
import 'moment-timezone';
import NoPreview from '../../Assets/not.svg'
class FavFileBrowser extends Component {

  constructor(props) {
    super(props)
    this.likes = (localStorage.getItem("likes"))? localStorage.getItem("likes") : ""
    this.buttons = [
      {
        name: "Delete",
        icon: "fas fa-trash-alt"
      }
    ]

    this.state = {
      cursorY: 0,
      cursorControl: 100,
      cursorPopup: 100,
      selectedButtonName: null,
      selectedControlButtonName: this.buttons[0].name,
      isControl: false,
      filenames: [],
      isPopup: false
    }
  }

  componentDidUpdate() {
    // if (this.refs.preview) {
    //   if (this.state.selectedButtonName) {
    //     axios.get('http://localhost:8080/previews/' + this.state.selectedButtonName)
    //       .then(response => {
    //         console.log(response)
    //         this.refs.preview.src = response.data
    //       })
    //   }
    // }
  }


  componentDidMount() {
    


    try {
      axios.get('http://localhost:8080/filenames')
        .then(response => {
          this.setState({ filenames: response.data.slice(0).reverse().filter(f=>this.likes.includes(f.split('_')[0])) })
          this.setState({ selectedButtonName: response.data[response.data.length - 1] })

          if (this.refs.preview) {
            if (this.state.selectedButtonName) {
              this.refs.preview.src = "http://localhost:8080/previews/" + this.state.selectedButtonName + ".png"
            }
          }

        })
    } catch (error) {
      console.log(error)
    }



    setTimeout(() => {
      this.refs.FileBrowser.style.opacity = "1";
      this.refs.FileBrowser.style.transform = "scale(1)";
    }, 10)
  }

  componentWillUnmount() {
    setTimeout(() => {
      // console.log("mod selek çıktı")
    }, 1000)
  }

  DeleteFile = (fn) => {
    axios.get('http://localhost:8080/delete?filename=' + fn)
      .then(response => {
        console.log(response.data)
      })
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorY = this.state.cursorY
    switch (socketData.payload) {
      case 'down':
        if (!this.state.isPopup) {
          if (!this.state.isControl) {
            tempCursorY++
          } else {
            // let temp = this.state.cursorControl
            // temp++
            // this.setState({ cursorControl: temp })
          }
        }
        break
      case 'up':
        if (!this.state.isPopup) {
          if (!this.state.isControl) {
            tempCursorY--
          } else {
            // let temp = this.state.cursorControl
            // temp--
            // this.setState({ cursorControl: temp })
          }
        }

        break
      case 'left':
        if (!this.state.isPopup) {
          this.setState({ isControl: !this.state.isControl })
        } else {
          let temp = this.state.cursorPopup
          temp--
          this.setState({ cursorPopup: temp })
        }
        break
      case 'movement':
        console.log("like", this.state.filenames[tempCursorY].substring(0, 5))
        
        if(this.likes.includes(this.state.filenames[tempCursorY].substring(0, 5))){
          console.log("sil")
          this.likes = this.likes.replace("." + this.state.filenames[tempCursorY].substring(0, 5), "")
          console.log(this.likes)
          localStorage.setItem("likes", this.likes)
        }else{
          console.log("ekle")
          localStorage.setItem("likes", ((localStorage.getItem("likes")) ? localStorage.getItem("likes") : "") + "." + this.state.filenames[tempCursorY].substring(0, 5))
          this.likes += "." + this.state.filenames[tempCursorY].substring(0, 5)
        }


        this.refs[this.state.filenames[tempCursorY].substring(0, 5)].classList.toggle("favorite-enabled");


        break
      case 'right':
        if (!this.state.isPopup) {
          this.setState({ isControl: !this.state.isControl })
        } else {
          let temp = this.state.cursorPopup
          temp++
          this.setState({ cursorPopup: temp })
        }
        break
      case 'zoomin':
        tempCursorY += 4
        break
      case 'zoomout':
        tempCursorY -= 4
        break
      case 'ok':
        if (!this.state.isPopup) {
          if (!this.state.isControl) {
            this.props.navigateTo("viewer", this.state.filenames[tempCursorY])
          } else {
            if ((this.state.cursorControl % 2 === 0)) {
              this.setState({ isPopup: true })
              this.refs.deletePopup.style.display = "flex"
            }
          }
        } else {
          if (this.state.cursorPopup % 2 === 0) {
            console.log("dosya sil")
            this.likes = (localStorage.getItem("likes"))? localStorage.getItem("likes") : ""
            this.likes = this.likes.replace("." + this.state.filenames[tempCursorY].substring(0, 5), "")
            console.log(this.likes)
            localStorage.setItem("likes", this.likes)

            this.DeleteFile(this.state.filenames[tempCursorY])
            setTimeout(() => {
              axios.get('http://localhost:8080/filenames')
                .then(response => {
                  this.setState({ filenames: response.data.slice(0).reverse().filter(f=>this.likes.includes(f.split('_')[0])) })
                  this.setState({ selectedButtonName: response.data[response.data.length - 1] })
                  this.setState({ isPopup: false })
                  this.refs.deletePopup.style.display = "none"
                  this.setState({
                    cursorY: tempCursorY,
                    selectedButtonName: this.state.filenames[tempCursorY],
                    isControl: false
                  })
                })
            }, 300);
          } else {
            console.log("dosya silme")
            this.refs.deletePopup.style.display = "none"
            this.setState({
              isPopup: false,
              isControl: true
            })
          }
        }

        break
      case 'back':
        if (!this.state.isPopup) {
          this.props.navigateTo("menuScreen")
        } else {
          this.setState({ isPopup: false })
          this.refs.deletePopup.style.display = "none"
        }
        return
      default:
        break
    }


    const currentColumnSize = this.state.filenames.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }



    try {

      this.refs.fileList.style.transform = "translateY(-" + (((tempCursorY) * 41) - Math.floor(223 / this.state.filenames.length) * tempCursorY) + "px)"
    } catch (e) {

    }




    this.setState({
      cursorY: tempCursorY,
      selectedButtonName: this.state.filenames[tempCursorY],
      selectedControlButtonName: this.buttons[this.state.cursorControl % 2].name
    })


    if (this.refs.preview) {
      if (this.state.selectedButtonName) {
        this.refs.preview.src = "http://localhost:8080/previews/" + this.state.selectedButtonName + ".png"
      }
    }
  }

  RenderTime() {
    return (
      <Moment format="HH:mm" unix>1559042615698</Moment>
    )
  }

  render() {
    return (
      
      <div className="file-browser" ref="FileBrowser">

        <div className="file-delete-popup" ref="deletePopup">
          <div className="file-delete-warning ">Do you really want to delete this file?</div>
          <div className="button-container">
            <div className={`file-delete-button ${(this.state.cursorPopup % 2 === 0) ? ' popup-selected' : ''}`}> <i className="far fa-trash-alt"></i> Yes</div>
            <div className={`file-delete-button ${(this.state.cursorPopup % 2 === 1) ? ' popup-selected' : ''}`}>No</div>
          </div>
        </div>

        <div className="file-container">
          <div className="file-list" ref="fileList">
            {
              this.state.filenames.map((element, key) => {
                let [fileNumber, timestamp] = element.split("_") //[0]
                if (timestamp > 1000) {
                  return (
                    <div key={key} className={`file${(element === this.state.selectedButtonName) ? ' selected' : ''}`}><i className="fas fa-file-invoice"></i>
                      <span style={{ fontWeight: 900 }}> {fileNumber + " "}</span>
                      <Moment style={{ marginLeft: 10 }} format="DD.MM.YYYY HH:mm" tz={window.localStorage.getItem("timezone")} unix>{timestamp}</Moment>
                      <i ref={fileNumber} className={`${(this.likes.includes(fileNumber) ? "favorite-enabled fas" : "fas")} fa-star favorite`}></i>
                    </div>
                  )
                } else {
                  return (
                    <div key={key} className={`file${(element === this.state.selectedButtonName) ? ' selected' : ''}`}><i className="fas fa-file-invoice"></i>
                      <span style={{ fontWeight: 900 }}> {fileNumber + " "}</span>
                    </div>
                  )
                }

              })
            }
          </div>

          <div className="file-control">
            <div className="preview-holder">
              <img alt="preview"  className="preview" ref="preview"></img>
            </div>

            {
              this.buttons.map((button, key) => {
                return (
                  <div key={key} className={`file-control-button${(button.name === this.state.selectedControlButtonName && this.state.isControl) ? ' selected' : ''}`}><i className={button.icon}></i> {button.name} </div>
                )
              })
            }
          </div>

        </div>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default FavFileBrowser