import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './Timezones.css'
import './Settings.css'

const timezoneList = require('../../Timezones.json')


class Timezones extends Component {

  constructor(props) {
    super(props)
    this.currentList = Object.keys(timezoneList)

    this.state = {
      cursorX: 0,
      cursorY: 0,
      listType: 'region',
      selectedRegion: '',
      selectedCity: ''
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.refs.settings.style.opacity = "1";
      this.refs.settings.style.transform = "scale(1)";
    }, 10)
    const tz = window.localStorage.getItem("timezone")
    console.log(tz.split('/')[0])
    this.setState({
      cursorY: this.findCursorForY(tz.split('/')[0])
    })
  }

  changeList() {
    const type = this.state.listType
    if (type === 'region') {
      this.currentList = Object.keys(timezoneList)
    } else if (type === 'city') {
      this.currentList = Object.keys(timezoneList[this.state.selectedRegion])
    }
    this.forceUpdate()
  }

  findCursorForY(key) {
    console.log(Object.keys(timezoneList).indexOf(key))
    return Object.keys(timezoneList).indexOf(key)
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursorY = this.state.cursorY
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'left':
        tempCursorY--
        break
      case 'right':
        tempCursorY++
        break
      case 'ok':
        if (this.state.listType === 'region') {
          this.setState({
            selectedRegion: this.currentList[this.state.cursorY],
            listType: 'city',
            cursorY: 0
          })
          this.changeList()
        } else if (this.state.listType === 'city') {
          this.setState({
            selectedCity: this.currentList[this.state.cursorY]
          })
          this.props.setTheme({
            type: "timezone",
            data: timezoneList[this.state.selectedRegion][this.state.selectedCity]
          })
        }
        break
      case 'back':
        if (this.state.listType === 'region') {
          this.props.navigateTo("menuScreen")
          return
        } else if (this.state.listType === 'city') {
          this.setState({
            listType: 'region',
            cursorY: this.findCursorForY(this.state.selectedRegion)
          })
          this.changeList()
        }
        break;
      default:
        break
    }

    const currentColumnSize = this.currentList.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    const currentRowSize = this.currentList.length

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      cursorX: tempCursorX,
      cursorY: tempCursorY
    })
  }

  render() {
    let infoText;
    if (this.state.listType === 'region') {
      infoText = "Please select your region."
    } else if (this.state.listType === 'city') {
      infoText = "Please select your city. Your clock is automatically set via GPS."
    }
    return (
      <div className="settings" ref="settings">
        <div className="time-zone-settings">
          <div className="title">Time Zone Settings</div>
          <div className="info">{ infoText }</div>

          <div className="select">{this.currentList[this.state.cursorY]}</div>
        </div>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default Timezones