import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './Theme.css'
import './Settings.css'
import blue from '../../Assets/themes/blue.jpg'
import green from '../../Assets/themes/green.jpg'
import orange from '../../Assets/themes/orange.jpg'
import black from '../../Assets/themes/black.jpg'
import gold from '../../Assets/themes/gold.jpg'

class Theme extends Component {
  themes = [
    {
      "name": "green",
      "image": green

    },
    {
      "name": "blue",
      "image": blue

    },
    {
      "name": "orange",
      "image": orange
    },
    {
      "name": "black",
      "image": black
    }
    ,
    {
      "name": "gold",
      "image": gold
    }
  ]

  constructor(props) {
    super(props)
    this.state = {
      cursorX: 0,
      cursorY: 0,
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.refs.settings.style.opacity = "1";
      this.refs.settings.style.transform = "scale(1)";
    }, 10)


    //sets the current theme
    let currentTheme = localStorage.getItem("theme")
    this.themes.map((e, i) => {
      if (e.name === currentTheme) {
        this.setState({
          cursorY: i
        })
      }
    })
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursorY = this.state.cursorY
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'left':
        tempCursorY--
        break
      case 'right':
        tempCursorY++
        break
      case 'ok':
        window.localStorage.setItem("theme", this.themes[this.state.cursorY].name)
        this.props.setTheme({
          type: "theme",
          data: this.themes[this.state.cursorY].name
        })
        break
      case 'back':
        this.props.navigateTo("menuScreen")
        return
      default:
        break
    }

    const currentColumnSize = this.themes.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    const currentRowSize = this.themes.length

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      cursorX: tempCursorX,
      cursorY: tempCursorY
    })
  }

  render() {
    return (
      <div className="settings" ref="settings">
        <div className="themes-settings">
          <div className="title">  {this.props.strings["themeSettingsScreen"]}</div>
          <div className="info"> {this.props.strings["themeInfo"]} </div>

          <div className="select">{this.themes[this.state.cursorY].name}</div>
          <img alt="" src={this.themes[this.state.cursorY].image}></img>
        </div>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default Theme