import React, { Component } from 'react'
import './ResistivityCalibration.css'
import SocketHelper from '../../SocketHelper'

class ResistivityCalibration extends Component {
  constructor(props) {
    super(props)

    this.lines = ["1-2", "2-3", "3-4", "1-4", "2-4", "1-3"]
    this.lineletters = ['A', 'B', 'C', 'D', 'E', 'F',]


    this.state = {
      cursorY: 100 * 3,
      cursorLine: 100 * 6,
      popup: false,
      sensorValue: 0
    }

  }

  componentDidMount() {
    console.log(this.refs.ref_a)
    this.refs.popup.style.display = "none"
  }

  componentDidUpdate() {
    if (this.state.popup) {
      this.refs.popup.style.display = "flex"
    } else {
      this.refs.popup.style.display = "none"
    }
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'up':
        if (!this.state.popup) {
          this.setState({
            cursorY: this.state.cursorY - 1
          })
        }
        break
      case 'down':
        if (!this.state.popup) {
          this.setState({
            cursorY: this.state.cursorY + 1
          })
        }
        break
      case 'left':
        if (this.state.cursorY % 3 === 0) {
          this.setState({
            cursorLine: this.state.cursorLine - 1
          })
        }
        break
      case 'right':
        if (this.state.cursorY % 3 === 0) {
          this.setState({
            cursorLine: this.state.cursorLine + 1
          })
        }
        break
      case 'ok':
        if (!this.state.popup && this.state.cursorY % 3 == 1) {
          this.getPropData(this.lineletters[this.state.cursorLine % 6])
          this.setState({
            popup: true
          })
        } else if (!this.state.popup && this.state.cursorY % 3 == 2) {
          this.refs.resetPopup.style.display = "flex"
          setTimeout(() => {
            this.refs.resetPopup.style.display = "none"
          }, 1200);
          this.setState({
            sensorValue: 0
          })
          localStorage.setItem("resCalibration", 0)
        }

        break
      case 'back':
        if (!this.state.popup) {
          setTimeout(() => {
            this.props.navigateTo("menuScreen")
          }, 300);
        }

        break
      default:
        break
    }
  }

  getPropData(line) {
    let counter = 0;
    let interval = setInterval(() => {
      counter++;

      if (counter > 4) {
        clearInterval(interval)
        localStorage.setItem("resCalibration", this.state.sensorValue)
        this.setState({
          popup: false
        })
        setTimeout(() => {
          this.closeProps()
        }, 850);
      } else {
        this.getResData(line)
      }
    }, 600)
  }

  getResData = async (line) => {

    let sensorResponse = await this.refs.socket.requestScan(line);
    console.log(sensorResponse)
    if (sensorResponse.type === "scan") {
      let dif = (200 - sensorResponse.payload);
      this.setState({ sensorValue: dif });
    }
  }

  closeProps = async (line) => {
    let sensorResponse = await this.refs.socket.requestScan("Z")
    console.log(sensorResponse)
  }


  render() {
    return (
      <div className="resistivity-calibration">
        <div className="calibration-popup" ref="popup">
          <div className="calibration-preloader"></div>
          <p>Calibrating...</p>
        </div>
        <div className="calibration-reset-popup" ref="resetPopup">
          Calibration reset.
        </div>
        <div className="calibration-image">
          <h4> <i className="fas fa-exclamation-triangle"></i> Calibration</h4>
          <p>
            Please select a line to calibrate the resistivity system and perform a scan in the area where you are sure of the soil level. After calibration, your resistivity searches will give you more healthy results.
          </p>
        </div>
        <div className={`calibration-button  ${(this.state.cursorY % 3 == 0) ? ' calibration-selected' : ''} `}>
          <div className="calibration-letter-holder">
            <div className={`calibration-letter  ${(this.state.cursorLine % 6 == 0) ? ' calibration-letter-selected' : ''} `}>A <span className="prop-nums">(1-2)</span></div>
            <div className={`calibration-letter  ${(this.state.cursorLine % 6 == 1) ? ' calibration-letter-selected' : ''} `}>B <span className="prop-nums">(2-3)</span></div>
            <div className={`calibration-letter  ${(this.state.cursorLine % 6 == 2) ? ' calibration-letter-selected' : ''} `}>C <span className="prop-nums">(3-4)</span></div>
            <div className={`calibration-letter  ${(this.state.cursorLine % 6 == 3) ? ' calibration-letter-selected' : ''} `}>D <span className="prop-nums">(1-4)</span></div>
            <div className={`calibration-letter  ${(this.state.cursorLine % 6 == 4) ? ' calibration-letter-selected' : ''} `}>E <span className="prop-nums">(2-4)</span></div>
            <div className={`calibration-letter  ${(this.state.cursorLine % 6 == 5) ? ' calibration-letter-selected' : ''} `}>F <span className="prop-nums">(1-3)</span></div>
          </div>

        </div>
        <div className={`calibration-button  ${(this.state.cursorY % 3 == 1) ? ' calibration-selected' : ''} `}>Start Calibration</div>
        <div className={`calibration-button  ${(this.state.cursorY % 3 == 2) ? ' calibration-selected' : ''} `}>Reset</div>
        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default ResistivityCalibration