import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './PopupSettings.css'

class PopupSettings extends Component {
  constructor(props) {
    super(props)
    const savedConfig = localStorage.getItem(this.props.type)
    let defaultButton = this.props.buttons.find(b => b.name === savedConfig)
    let cursor = 0
    if (defaultButton) {
      cursor = this.props.buttons.findIndex(b => b === defaultButton)
      defaultButton = defaultButton.name
    } else {
      defaultButton = false
    }
    this.state = {
      cursorX: cursor,
      cursorY: cursor,
      selectedButtonName: defaultButton || this.props.buttons[0].name
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.refs.popup.style.opacity = "0.95";
      this.refs.popup.style.transform = "scale(1)";
    }, 10)

  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursorY = this.state.cursorY
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'right':
        tempCursorY++
        // this.refs.sound.play()
        break
      case 'left':
        tempCursorY--
        // this.refs.sound.play()
        break
      case 'ok':
        //Seçilen düğmenin hedefine gidiyoruz
        this.props.handleSelect({
          type: this.props.type,
          data: this.state.selectedButtonName
        })
        this.props.goBack()
        return
      case 'back':
        this.props.goBack()
        return
      default:
        break
    }

    const currentColumnSize = this.props.buttons.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    const currentRowSize = this.props.buttons[tempCursorY].length

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      cursorX: tempCursorX,
      cursorY: tempCursorY,
      selectedButtonName: this.props.buttons[tempCursorY].name
    })
  }


  render() {
    return (
      <div  className='popup-container'>
        <div className='background' />
        <div style={this.props.strings.theme["Popup"]["PopupSettings"]} className="popup-settings" ref="popup">
          <div style={this.props.strings.theme["Popup"]["Title"]} className="popup-title">{this.props.strings[this.props.title]}</div>
          <div style={this.props.strings.theme["Popup"]["Button"]} className="popup-button">{this.props.strings[this.state.selectedButtonName] }</div>
          <div style={this.props.strings.theme["Popup"]["Info"]} className="info">{this.props.strings["popup_text"]}</div>
        </div>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default PopupSettings