import React, { Component } from 'react'
import './SoundSettings.css'
import SocketHelper from '../../SocketHelper'

class SoundSettings extends Component {

  constructor(props) {
    super(props)

    this.state = {
      volume: 50,
      cursorY: 1000,
      vibration: false
    }
  }

  componentDidMount() {
    if (localStorage.getItem("volume")) {
      this.setState({
        volume: parseInt(localStorage.getItem("volume"))
      })
    } else {
      localStorage.setItem("volume", 50)
    }

    if (localStorage.getItem("vibration")) {
      this.setState({
        vibration: (localStorage.getItem("vibration") === 'true')
      })
    } else {
      localStorage.setItem("vibration", false)
    }

    this.refs.pin.style.transform = `translate(${-7 + this.state.volume * 2.2}px, -7px)`
    setTimeout(() => {
      this.refs.soundSettings.style.opacity = 1
      this.refs.soundSettings.style.transform = "scale(1)"

    }, 100);
  }

  componentDidUpdate() {
    this.refs.pin.style.transform = `translate(${-7 + this.state.volume * 2.2}px, -7px)`
  }

  componentWillUnmount() {
    localStorage.setItem("volume", this.state.volume)
  }

  handleKeyDown = (socketData) => {
    var level = 0
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'right':
        if (this.state.volume < 100 && this.state.cursorY % 2 === 0) {
          level = this.state.volume;
          level += 10;
          this.setState({ volume: level })
        }
        break
      case 'left':
        if (this.state.volume > 0 && this.state.cursorY % 2 === 0) {
          level = this.state.volume;
          level -= 10;
          this.setState({ volume: level })
        }
        break
      case 'up':
        this.setState({
          cursorY: this.state.cursorY + 1
        })
        break
      case 'down':
        this.setState({
          cursorY: this.state.cursorY - 1
        })
        break
      case 'ok':
        if (this.state.cursorY % 2 === 1) {
          this.setState({
            vibration: !this.state.vibration
          })
          localStorage.setItem("vibration", this.state.vibration)
        }

        break
      case 'back':
        this.refs.soundSettings.style.opacity = 0
        this.refs.soundSettings.style.transform = "scale(5)"
        setTimeout(() => {
          this.props.navigateTo("menuScreen")

        }, 200);
        return
      default:
        break
    }
  }


  render() {
    return (
      <div className="sound-settings" ref="soundSettings">
        <div className="sound-settings-popup">
          <div className="title">Volume Level</div>
          <div className={`slider-container ${(this.state.cursorY % 2 === 0) ? 'selected' : ''}`}>
            <i style={{ display: ((this.state.volume === 0) ? "none" : "inline") }} className="fas fa-volume-up volume-icon"></i>
            <i style={{ display: ((this.state.volume !== 0) ? "none" : "inline") }} className="fas fa-volume-mute volume-icon"></i>
            <div className="volume-slider">
              <div ref="pin" className="volume-pin">
                <div className="volume-indicator">{this.state.volume + "%"}</div>
              </div>
            </div>

          </div>

          <div className={`slider-container ${(this.state.cursorY % 2 === 1) ? 'selected' : ''}`}>
            <i style={{ display: ((this.state.vibration) ? "none" : "inline") }} className="far fa-circle checkbox"></i>  <i style={{ display: ((!this.state.vibration) ? "none" : "inline") }} className="far fa-check-circle checkbox"></i>  <div className="vibration"> Vibration </div>
          </div>

        </div>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default SoundSettings