import React, { Component } from 'react'
import './ResistivityManualResult.css'
import SocketHelper from '../../SocketHelper'
import * as THREE from "three";
import Colours from './Colours.json'


class ResistivityManualResult extends Component {

  componentDidMount() {
    console.log(this.props.result)
    this.results = this.props.result
    this.closeProps()


    const width = this.refs.canvasHolder.clientWidth;
    const height = this.refs.canvasHolder.clientHeight;
    this.scene = new THREE.Scene();
    this.camera = new THREE.PerspectiveCamera(56, width / height, 0.1, 1000);
    // this.camera = new THREE.OrthographicCamera(viewsize * aspect / - 2, viewsize * aspect / 2, viewsize / 2, viewsize / - 2, -100, 100);
    this.renderer = new THREE.WebGLRenderer({ antialias: false, alpha: true, preserveDrawingBuffer: true });
    this.renderer.setSize(width, height);

    this.renderer.autoClear = true;
    this.renderer.setClearColor(0xffffff, 0)
    this.refs.canvasHolder.appendChild(this.renderer.domElement);
    this.initializeCamera();



    const geometry = new THREE.PlaneGeometry(3, 4, 1, 1);
    // const geometry = new THREE.BoxGeometry(1, 1, e_matrix.length - 1);
    this.material = new THREE.MeshBasicMaterial({ color: 0xFFFFFF, wireframe: false, side: THREE.DoubleSide, vertexColors: THREE.FaceColors });
    this.cube = new THREE.Mesh(geometry, this.material);

    this.cube.geometry.vertices[0].y = 1.95
    this.cube.geometry.vertices[0].x = -1.6
    this.cube.geometry.vertices[1].y = 2.43
    this.cube.geometry.vertices[2].y = -1.5
    this.cube.geometry.vertices[3].y = -1.1

    this.cube.geometry.vertices[2].x = -1
    this.cube.geometry.vertices[3].x = 1.2
   

    this.setColors(this.props.result.value.payload)
    this.refs.value.innerHTML = this.props.result.value.payload
    this.refs.type.innerHTML = this.GetType(this.props.result.value.payload)
    this.scene.add(this.cube);
    this.animate();


  }

  setColors(val){

    this.cube.geometry.faces[0].vertexColors[0] = new THREE.Color("green")
    this.cube.geometry.faces[0].vertexColors[1] = new THREE.Color(this.colorFromArray(val))
    this.cube.geometry.faces[0].vertexColors[2] = new THREE.Color("green")

    this.cube.geometry.faces[1].vertexColors[0] = new THREE.Color(this.colorFromArray(val))
    this.cube.geometry.faces[1].vertexColors[1] = new THREE.Color(this.colorFromArray(val))
    this.cube.geometry.faces[1].vertexColors[2] = new THREE.Color("green")
  }

  GetType = (val) => {
    switch (true) {
      case (val < 20):
        return "notConnect"
      case (val < 105):
        return "cavity"
      case (val < 294):
        return "ground"
      case (val < 440):
        return "water"
      case (val < 490):
        return "mineral"
      case (val < 605):
        return "metal"
      case (val < 1023):
        return "short"
      default:
        break;
    }
  }

  closeProps = async () => {
    let sensorResponse = await this.refs.socket.requestScan('Z')
  }

  colorFromArray = (val) => {
    let mapped = (val / 1023) * 474
    return new THREE.Color(Colours[Math.trunc(mapped)])
  }

  initializeCamera() {
    this.camera.position.x = 0.6;
    this.camera.position.y = 0.5;
    this.camera.position.z = 7
    this.camera.rotation.y = -0.17
    this.camera.rotation.z = 0

  }

  animate = () => {
    this.frameId = window.requestAnimationFrame(this.animate);
    this.renderer.render(this.scene, this.camera);
        // this.camera.rotation.x = Math.PI/22
        // this.cube.rotation.y += 0.001


  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'back':
        this.props.navigateTo("menuScreen")
        return
      default:
        break
    }

  }


  render() {
    return (
      <div className="resistivity-manual-result">





        <div className="canvas-container-manual" ref="canvasHolder">  </div>

        <div className="right-panel">
          <div className="result-panel">
            <span className="result-panel-title">Line: </span> <span ref="line" className="result-panel-value">A</span>
          </div>
          <div className="result-panel">
            <span className="result-panel-title">Value: </span> <span ref="value" className="result-panel-value">x</span>
          </div>
          <div className="result-panel">
            <span className="result-panel-title">Material: </span> <span ref="type" className="result-panel-value">Not Connected</span>
          </div>
        </div>
        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default ResistivityManualResult