import React, { Component } from 'react'

import "./GroundScan.css"



import ScanAutoProperties from './ScanAutoProperties'
import ScanManualProperties from './ScanManualProperties'
import Scanning from './Scanning'

class GroundScan extends Component {

  constructor(props) {
    super(props)

    this.mode = (this.props.mode === "auto")? "scanAutoPropertiesScreen" : "scanManualPropertiesScreen"

    //scanAutoPropertiesScreen
    //scanManualPropertiesScreen
    this.state = {
      activeScreen: this.mode,
      scanOptions: {}
    }
  }

  groundNavigateTo = (activeScreen, scanOptions) => {
    this.setState({ activeScreen, scanOptions })
  }

  renderScreen = () => {
    switch (this.state.activeScreen) {
      case 'scanAutoPropertiesScreen':
        return <ScanAutoProperties strings={this.props.strings} groundNavigateTo={this.groundNavigateTo} navigateTo={this.props.navigateTo}/>
      case 'scanManualPropertiesScreen':
        return <ScanManualProperties strings={this.props.strings} groundNavigateTo={this.groundNavigateTo} navigateTo={this.props.navigateTo} />
      case 'scanningScreen':
        return <Scanning groundNavigateTo={this.groundNavigateTo} navigateTo={this.props.navigateTo} scanOptions={this.state.scanOptions}/>
      default:
        break;
    }
  }

  render() {
    return (
      <div className="ground-scan">
        {this.renderScreen()}
      </div>
    )
  }
}

export default GroundScan