import React, { Component } from 'react';
import './App.css';


import MenuContainer from './Components/Menu/MenuContainer'
import StatusBar from './Components/StatusBar/StatusBar'
import Setup from './Components/Setup/Setup'
import Preloader from './Components/Preloader/Preloader'
import Resistivity from './Components/Resistivity/Resistivity'
import GroundScan from './Components/GroundScan/GroundScan'
import Charging from './Components/Charging/Charging'
import ResistivityAuto from './Components/Resistivity/resistivityAuto'
import ResistivityManual from './Components/Resistivity/resistivityManual'
import FileBrowser from './Components/FileBrowser/FileBrowser'
import FavFileBrowser from './Components/FileBrowser/FavFileBrowser'
import ResFileBrowser from './Components/FileBrowser/ResFileBrowser'
import Viewer from './Components/FileBrowser/Viewer'
import Timezones from './Components/Settings/TimezoneSettings'
import ThemesSettings from './Components/Settings/Theme'
import LineScan from './Components/LiveScan/LineScan'
import Activation from './Components/Settings/Activation'
import ResistivityAutoResult from './Components/Resistivity/resistivityAutoResult'
import FactoryReset from './Components/Settings/FactoryReset'
import TurnOff from './Components/TurnOff/TurnOff'
import BackgroundColor from './Components/Settings/BackgroundColor'
import PinPoint from './Components/LiveScan/PinPoint'
import Dynamic from './Components/LiveScan/Dynamic'
import Histogram from './Components/LiveScan/Histrogram'
import SoundSettings from './Components/Settings/SoundSettings'
import HardwareInfo from './Components/DeviceInfo/HardwareInfo'
import SoftwareInfo from './Components/DeviceInfo/SoftwareInfo'
import ResistivityManualResult from './Components/Resistivity/ResistivityManualResult'
import TurningOff from './Components/TurnOff/TurningOff'
import ResistivityCalibration from './Components/Resistivity/ResistivityCalibration'

import PopupSettings from './Components/Settings/PopupSettings'

const Strings = require('./Strings.json')
const Themes = require('./Themes.json')

class App extends Component {

  constructor(props) {
    super(props)

    // menuScreen
    // preloaderScreen 
    // setupScreen 
    // resistivityScreen 
    // popupSettings 
    // chargingScreen 
    // groundAutoScanScreen 
    // groundManualScanScreen
    // fileBrowser 
    // viewer 
    // timeZoneSettingsScreen 
    // themeSettingsScreen 
    // lineScanScreen
    // pinPointScanScreen
    // histogramScanScreen
    // activationScreen
    // resistivityAutoResultScreen
    // factoryResetScreen
    // turnOffScreen
    // backgroundColorScreen
    // softwareInfoScreen
    // hardwareInfoScreen
    // soundSettingsScreen
    // resistivityManualResultScreen
    // turningOffScreen
    // resistivityCalibration

    this.state = {
      activeScreen: (localStorage.getItem('setupCompleted') === 'true') ? 'menuScreen' : 'setupScreen',
      language: localStorage.getItem('language') || 'en',
      theme: localStorage.getItem('theme') || 'green',
      timezone: localStorage.getItem('timezone') || 'Europe/Istanbul',
      fileToOpen: null,
      background_3d: localStorage.getItem('background_3d') || 'black',
      background_scan: localStorage.getItem('background_scan') || 'black',
      ResistivityData: {}
    }
  }

  navigateTo = (activeScreen, fileToOpen) => {
    this.setState({ fileToOpen: fileToOpen })
    this.setState({ activeScreen })
  }

  getResistivityData = (data) => {
    this.setState({ ResistivityData: data })
  }


  renderScreen = () => {
    let stringsObj = Strings[this.state.language]
    stringsObj.theme = Themes[this.state.theme]

    switch (this.state.activeScreen) {
      case 'popupSettings':
        return <PopupSettings strings={stringsObj} navigateTo={this.navigateTo} />
      case 'preloaderScreen':
        return <Preloader navigateTo={this.navigateTo} />
      case 'resistivityScreen':
        return <Resistivity navigateTo={this.navigateTo} />
      case 'resistivityAuto':
        return <ResistivityAuto strings={stringsObj} getResistivityData={this.getResistivityData} navigateTo={this.navigateTo} />
      case 'resistivityManual':
        return <ResistivityManual strings={stringsObj} getResistivityData={this.getResistivityData} navigateTo={this.navigateTo} />
      case 'groundAutoScanScreen':
        return <GroundScan strings={stringsObj} navigateTo={this.navigateTo} mode="auto" />
      case 'groundManualScanScreen':
        return <GroundScan strings={stringsObj} navigateTo={this.navigateTo} mode="manual" />
      case 'setupScreen':
        return <Setup navigateTo={this.navigateTo} setTheme={this.popupSelect} strings={stringsObj} />
      case 'menuScreen':
        return <MenuContainer popupSelect={this.popupSelect} strings={stringsObj} navigateTo={this.navigateTo} />
      case 'chargingScreen':
        return <Charging />
      case 'fileBrowser':
        return <FileBrowser navigateTo={this.navigateTo} timezone={this.state.timezone} strings={stringsObj} />
      case 'favFileBrowser':
        return <FavFileBrowser navigateTo={this.navigateTo} timezone={this.state.timezone} />
      case 'resFileBrowser':
        return <ResFileBrowser getResistivityData={this.getResistivityData} navigateTo={this.navigateTo} timezone={this.state.timezone} />
      case 'viewer':
        return <Viewer open={this.state.fileToOpen} navigateTo={this.navigateTo} />
      case 'timeZoneSettingsScreen':
        return <Timezones navigateTo={this.navigateTo} setTheme={this.popupSelect} />
      case 'themeSettingsScreen':
        return <ThemesSettings strings={stringsObj} navigateTo={this.navigateTo} setTheme={this.popupSelect} />
      case 'lineScanScreen':
        return <LineScan navigateTo={this.navigateTo} />
      case 'pinPointScanScreen':
        return <PinPoint navigateTo={this.navigateTo} />
      case 'histogramScanScreen':
        return <Histogram navigateTo={this.navigateTo} />
      case 'dynamicScanScreen':
        return <Dynamic navigateTo={this.navigateTo} />
      case 'activationScreen':
        return <Activation navigateTo={this.navigateTo} />
      case 'resistivityAutoResultScreen':
        return <ResistivityAutoResult result={this.state.ResistivityData} strings={stringsObj} navigateTo={this.navigateTo} />
      case 'factoryResetScreen':
        return <FactoryReset navigateTo={this.navigateTo} />
      case 'turnOffScreen':
        return <TurnOff strings={stringsObj} navigateTo={this.navigateTo} />
      case 'backgroundColorScreen':
        return <BackgroundColor strings={stringsObj} navigateTo={this.navigateTo} setTheme={this.popupSelect} />
      case 'hardwareInfoScreen':
        return <HardwareInfo strings={stringsObj} navigateTo={this.navigateTo} />
      case 'softwareInfoScreen':
        return <SoftwareInfo strings={stringsObj} navigateTo={this.navigateTo} />
      case 'soundSettingsScreen':
        return <SoundSettings strings={stringsObj} navigateTo={this.navigateTo} />
      case 'resistivityManualResultScreen':
        return <ResistivityManualResult result={this.state.ResistivityData} strings={stringsObj} navigateTo={this.navigateTo} />
      case 'turningOffScreen':
        return <TurningOff strings={stringsObj} navigateTo={this.navigateTo} />
      case 'resistivityCalibration':
        return <ResistivityCalibration strings={stringsObj} navigateTo={this.navigateTo} />
      default:
        break;
    }
  }

  popupSelect = (setting0, setting1) => {
    switch (setting0.type) {
      case 'language':
        this.setState({
          language: setting0.data
        })
        localStorage.setItem(setting0.type, setting0.data)
        break
      case 'theme':
        this.setState({
          theme: setting0.data
        })
        localStorage.setItem('theme', setting0.data)
        break
      case 'timezone':
        this.setState({
          timezone: setting0.data
        })
        localStorage.setItem('timezone', setting0.data)
        break
      case 'background':
        this.setState({
          background_3d: setting0.data,
          background_scan: setting1.data
        })
        localStorage.setItem('background_3d', setting0.data)
        localStorage.setItem('background_scan', setting1.data)
        break
      default:
        break
    }
  }

  render() {
    return (
      <div ref='screen' style={{ backgroundImage: `url('./${this.state.theme}.png')` }} className="App">
        <StatusBar title={this.state.activeScreen} timezone={this.state.timezone} strings={Strings[this.state.language]} />
        {this.renderScreen()}
      </div>
    );
  }
}

export default App;

