import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'


export default class MenuPanel extends Component {
  constructor(props) {
    super(props)
    this.state = {
      cursorX: 0,
      cursorY: 0,
      selectedButtonName: this.props.buttons[0].name,
      brightness: 100
    }
    this.iconWidth = 19.3 + (15 * 2)
    this.panelDefaultLeftOffset = 24
    this.defaultTransformX = (this.iconWidth * this.props.stackCount)
    if (this.props.stackCount > 0) {
      this.defaultTransformX -= 10 + this.panelDefaultLeftOffset
    }
    this.defaultStyle = {
      // (stack sayısı * 50) + bir miktar
      transform: `translateX(${this.defaultTransformX - 200}px)`,
      zIndex: Math.floor(1000 / (this.props.stackCount + 1)),
      opacity: this.props.focused ? 0 : 1
    }
  }

  componentDidMount() {
    // this.refs.audio.volume = 1

    // Soldaki pay: this.refs.panel.offsetLeft
    // Logo: 19.3
    // Logo sağ sol pay: 15
    // StackCount => - ( w - (stackCount * (logoWidth + (logoPay*2)) + panelSolPay)  )
    if (this.props.stackCount > 0) {
      this.refs.panel.style.borderTopLeftRadius = 0
      this.refs.panel.style.borderBottomLeftRadius = 0
    }
    if (this.props.focused) {
      setTimeout(() => {
        this.refs.panel.className = "menu-panel reveal"
        this.refs.panel.style.transform = `translateX(${this.defaultTransformX}px)`
      }, 150)
      // this.refs.panel.style.opacity = 1
    }
  }

  componentDidUpdate() {
    this.refs.panel.style.filter = `brightness(${this.state.brightness}%)`
    this.props.sendCursorY(this.state.cursorY)
  }

  collapse = () => {
    // Soldaki pay: this.refs.panel.offsetLeft
    // Logo: 19.3
    // Logo sağ sol pay: 15
    // StackCount => - ( w - (stackCount * (logoWidth + (logoPay*2)) + panelSolPay)  )
    let w = this.refs.panel.clientWidth
    let panelLeftOffset = this.refs.panel.offsetLeft
    this.translateX = -(w - (this.props.stackCount * (this.iconWidth)) + panelLeftOffset)
    this.refs.panel.style.transform = `translateX(${this.translateX}px)`
  }

  expand = () => {
    this.refs.panel.style.transform = `translateX(${this.defaultTransformX}px)`
  }

  addBrightness = (percent) => {
    this.setState({
      brightness: this.state.brightness + percent
    })
  }

  handleKeyDown = (socketData) => {
    if (!this.props.focused) { return }
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursorY = this.state.cursorY
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'right':
        //Seçilen düğmenin hedefine gidiyoruz
        if (this.props.buttons[this.state.cursorY].target) {
          this.props.navigateTo(this.props.buttons[this.state.cursorY].target)
          return
        }
        else {
          this.props.handleSelect(this.props.buttons[this.state.cursorY])
          if (this.props.buttons[this.state.cursorY].hasPopup) {
            // this.props.blur(true)
          } else {
            this.collapse()
          }
          return
        }
      case 'left':
        this.props.goBack()
        return
      case 'up':
        // this.refs.audio.currentTime = 0;
        // this.refs.audio.play()
        tempCursorY--
        possibleDiagonalJump = true
        break
      case 'down':
        // this.refs.audio.currentTime = 0;
        tempCursorY++
        // this.refs.audio.play()

        possibleDiagonalJump = true
        break
      case 'turnoff':
        this.props.navigateTo("turnOffScreen")
        return
      case 'ok':
        //Seçilen düğmenin hedefine gidiyoruz
        if (this.props.buttons[this.state.cursorY].target) {
          this.props.navigateTo(this.props.buttons[this.state.cursorY].target)
          return
        }
        else {
          this.props.handleSelect(this.props.buttons[this.state.cursorY])
          if (this.props.buttons[this.state.cursorY].hasPopup) {
            // this.props.blur(true)
          } else {
            this.collapse()
          }
          return
        }
      case 'back':
        // this.props.navigateTo('mainMenuScreen')
        this.props.goBack()
        return
      default:
        break
    }

    const currentColumnSize = this.props.buttons.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    const currentRowSize = this.props.buttons[tempCursorY].length

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      cursorX: tempCursorX,
      cursorY: tempCursorY,
      selectedButtonName: this.props.buttons[tempCursorY].name
    })
  }

  renderButton = (button, key) => {
    let className = `menu-button ${this.state.selectedButtonName === button.name ? 'selected' : ''}`
    return (
      <div key={key} style={this.props.strings.theme["MenuPanel"]} className={className}>
        <span style={this.props.strings.theme["MenuPanel"]["Text"]}>{this.props.strings[button.name]}</span>
        <i className={button.icon} />
      </div>
    )
  }

  render() {
    return (
      <div ref='panel' style={this.defaultStyle} className={`menu-panel`}>
        {
          this.props.buttons.map((button, key) => {
            return this.renderButton(button, key)
          })
        }
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}