import React, { Component } from 'react';
import MenuPanel from './MenuPanel'
import './MenuContainer.css'
import PopupSettings from '../Settings/PopupSettings';
import SocketHelper from '../../SocketHelper'


import Scan from '../../Assets/menuIcons/scan.png'
import LiveScan from '../../Assets/menuIcons/live_scan.png'
import Resistivity from '../../Assets/menuIcons/resistivity.png'
import Settings from '../../Assets/menuIcons/settings.png'
import Files from '../../Assets/menuIcons/files.png'



const buttons = require('./MenuLayout.json')

class MenuContainer extends Component {

  constructor(props) {
    super(props)
    let setOpa;
    this.state = {
      currentButtons: buttons,
      panels: [
        buttons
      ],
      activePanelIndex: 0,
      popupActive: false,
      popupTitle: '',
      popupElements: [],
      flashlight: 0
    }
  }

  sendLightLevel = async (lightLevel) => {
    let sensorResponse = await this.refs.socket.requestScan(lightLevel.toString())
    console.log(sensorResponse)
  }


  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'zoomin':
        if (this.state.flashlight < 2) {
          this.setState({
            flashlight: this.state.flashlight + 1
          })
          this.sendLightLevel(this.state.flashlight)
        }
        console.log("flash light level:", this.state.flashlight)
        break
      case 'zoomout':
        if (this.state.flashlight > 0) {
          this.setState({
            flashlight: this.state.flashlight - 1
          })
          this.sendLightLevel(this.state.flashlight)
        }
        console.log("flash light level:", this.state.flashlight)
        break
      case 'ok':

        return
      default:
        break
    }
  }

  componentWillUnmount(){
    clearTimeout(this.setOpa)
    clearTimeout(this.setPic)
  }


  handleButtonSelect = (selectedButton) => {
    if (selectedButton.children) {
      if (selectedButton.children.length > 0) {
        if (selectedButton.hasPopup) {
          // POPUP
          this.setState({
            popupActive: true,
            popupTitle: selectedButton.title,
            popupElements: selectedButton.children,
            popupType: selectedButton.name
          })
        } else { // Popup değil, yeni panel
          // Navigasyon
          let tempPanels = [...this.state.panels]
          tempPanels.push(selectedButton.children)
          // Change brightness
          for (let i = 0; i < this.state.panels.length; i++) {
            this.refs[`panel${i}`].addBrightness(-8);
          }
          this.setState({
            panels: tempPanels,
            activePanelIndex: this.state.activePanelIndex + 1
          })
        }
      }
    }
    // App.js rapor
  }

  popPanel = () => {
    if (this.state.popupActive) {
      this.setState({
        popupActive: false
      })
      return
    }
    if (this.state.activePanelIndex === 0) {
      // 
      return
    }
    let tempPanels = [...this.state.panels]
    tempPanels.pop()
    // Change brightness
    for (let i = 0; i < this.state.panels.length; i++) {
      this.refs[`panel${i}`].addBrightness(8);
    }
    this.setState({
      panels: tempPanels,
      activePanelIndex: this.state.activePanelIndex - 1
    }, () => {
      this.refs[`panel${this.state.activePanelIndex}`].expand()
    })
  }


  getCursorY = (cursorY) => {
    if (this.state.activePanelIndex === 0) {
      clearTimeout(this.setOpa)
      this.refs.menuImage.style.opacity = 0
      let setPic = setTimeout(() => {
        try {
          switch (cursorY) {
            case 0:
              this.refs.menuImage.src = Scan
              break
            case 1:
              this.refs.menuImage.src = LiveScan
              break
            case 2:
              this.refs.menuImage.src = Resistivity
              break
            case 3:
              this.refs.menuImage.src = Files
              break
            case 4:
              this.refs.menuImage.src = Settings
              break
          }
        } catch (error) {
          
        }


      }, 400);

      this.setOpa = setTimeout(() => {
        this.refs.menuImage.style.opacity = 1
      }, 550);

    }

  }





  render() {
    return (
      <div>
        <div className='menu-container'>
          {
            this.state.panels.map((panel, key) => {
              return <MenuPanel sendCursorY={this.getCursorY} navigateTo={this.props.navigateTo} strings={this.props.strings} key={key} ref={`panel${key}`} stackCount={this.state.panels.length - 1} buttons={panel} handleSelect={this.handleButtonSelect} goBack={this.popPanel} focused={!this.state.popupActive && this.state.activePanelIndex === key} />
            }
            )
          }

        </div>
        {this.state.popupActive && <PopupSettings type={this.state.popupType} handleSelect={this.props.popupSelect} title={this.state.popupTitle} strings={this.props.strings} goBack={this.popPanel} buttons={this.state.popupElements} />}
        <img className="menu-icon" ref="menuImage" src={Scan} />

        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    );
  }
}

export default MenuContainer;

