import React, { Component } from 'react'
import './FactoryReset.css'
import SocketHelper from '../../SocketHelper'
import axios from 'axios'

import ResetImage from '../../Assets/reset.png'

class FactoryReset extends Component {

  constructor(props) {
    super(props)

    this.state = {
      cursorY: true,
      isSoft: false,
      isDeleting: false,
      isHardReseting: false,
      isHard: false,
      cursorPopup: 100,
      fileCount: 0
    }
  }

  componentDidMount() {
    axios.get('http://localhost:8080/filenames')
      .then(response => {
        this.setState({ fileCount: response.data.length })
      })
  }



  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'down':
        if (!this.state.isSoft && !this.state.isDeleting) {
          this.setState({ cursorY: !this.state.cursorY })
        }
        break
      case 'up':
        if (!this.state.isSoft && !this.state.isDeleting) {
          this.setState({ cursorY: !this.state.cursorY })
        }
        break
      case 'left':
        if (this.state.isSoft || this.state.isHard) {
          let temp = this.state.cursorPopup;
          temp++
          this.setState({ cursorPopup: temp })
        }
        break
      case 'right':
        if (this.state.isSoft || this.state.isHard) {
          let temp = this.state.cursorPopup;
          temp--
          this.setState({ cursorPopup: temp })
        }
        break
      case 'ok':
        if ((this.state.isSoft === false)) {
          if (this.state.cursorY === true) {
            this.setState({ isSoft: true })
          }
        }
        else if (this.state.isSoft && this.state.cursorPopup % 2 === 1) {
          this.setState({ isSoft: false })
        }
        else if (this.state.isSoft && this.state.cursorPopup % 2 === 0) {
          this.setState({ isDeleting: true, isSoft: false })
        }

        if ((this.state.isHard === false)) {
          if (this.state.cursorY === false) {
            this.setState({ isHard: true })
          }
        }
        else if (this.state.isHard && this.state.cursorPopup % 2 === 1) {
          this.setState({ isHard: false })
        }
        else if (this.state.isHard && this.state.cursorPopup % 2 === 0) {
          console.log("hado")
          this.setState({ isHardReseting: true, isHard: false })
        }



        break
      case 'back':
        if (!this.state.isSoft && !this.state.isHard && !this.state.isDeleting) {
          this.props.navigateTo("menuScreen")
        }

        return
      default:
        break
    }

  }

  softResetPopup = () => {
    return (
      <div className="soft-reset-popup" ref="softReset">
        <div className="soft-reset-warning">You are about to delete all local scan files. Are you sure?</div>
        <div className="button-container">
          <div className={`file-delete-button ${(this.state.cursorPopup % 2 === 0) ? ' popup-selected' : ''}`}> <i className="far fa-trash-alt"></i> Yes</div>
          <div className={`file-delete-button ${(this.state.cursorPopup % 2 === 1) ? ' popup-selected' : ''}`}>No</div>
        </div>
      </div>
    )
  }

  hardResetPopup = () => {
    return (
      <div className="soft-reset-popup" ref="softReset">
        <div className="soft-reset-warning">All settings and file will be deleted. Your device will reboot with factory settings?</div>
        <div className="button-container">
          <div className={`file-delete-button ${(this.state.cursorPopup % 2 === 0) ? ' popup-selected' : ''}`}> <i className="far fa-trash-alt"></i> Yes</div>
          <div className={`file-delete-button ${(this.state.cursorPopup % 2 === 1) ? ' popup-selected' : ''}`}>No</div>
        </div>
      </div>
    )
  }

  softResetDeleting = () => {
    if (this.state.fileCount !== 0) {
      let i = 0
      let percent = 0
      let dec = 100 / this.state.fileCount
      this.DeleteFile()
      setInterval(() => {
        if (i < this.state.fileCount) {
          this.refs.bar.style.width = percent + "%"
          percent = percent + dec;
        }
        if (i === this.state.fileCount) {

          this.setState({ isDeleting: false, fileCount: 0 })
        }
        i++
      }, 100);
      return (
        <div className="soft-reset-popup" ref="softReset" style={{ height: '90px' }}>
          <div className="soft-reset-warning">Your files are being deleted</div>
          <div className="soft-popup-progress-bar"><div ref="bar" className="moving-bar"></div></div>
        </div>
      )
    }
  }

  hardResetDeleting = () => {
    this.DeleteFile()
    localStorage.setItem("setupCompleted", "false")
    localStorage.setItem("language", "en")
    localStorage.setItem("likes","")
    setTimeout(() => {
      console.log("REBOOT REBOOT REBOOT")
      this.refs.socket.requestReboot()
    }, 3400);
    return (
      <div className="hard-reset-deleting">
        <div className="hard-reset-deleting-title">Grountech Discovery will turn to factory reset now.</div>
        <div className="hard-reset-deleting-title">Please <u>do not</u> remove batteries!</div>
        <img alt="delete" className="hard-reset-deleting-image" src={ResetImage}></img>
      </div>
    )
  }

  DeleteFile = (fn) => {
    axios.get('http://localhost:8080/deleteall')
      .then(response => {
        console.log(response.data)
      })
  }



  render() {
    return (
      <div className="factory-reset">

        {
          (this.state.isSoft ? this.softResetPopup() : '')
        }

        {
          (this.state.isDeleting ? this.softResetDeleting() : '')
        }

        {
          (this.state.isHard ? this.hardResetPopup() : '')
        }

        {
          (this.state.isHardReseting ? this.hardResetDeleting() : '')
        }





        <div id="sec1" className="section">
          <div className="title">SOFT RESET</div>
          <div className="info">Soft reset will delete all your scan files. Your settings will remain the same.</div>
          <div className={`reset-button ${(this.state.cursorY) ? 'selected-reset-button' : ''}`}>Perform a soft reset</div>
          <i className="fas fa-trash"></i>
        </div>

        <div id="sec2" className="section">
          <div className="title">FACTORY RESET</div>
          <div className="info"> Before we get started, know that a factory reset will erase all the data and settings on your device!</div>
          <div className={`reset-button ${(!this.state.cursorY) ? 'selected-reset-button' : ''}`}>Perform a factory reset</div>
          <i className="fas fa-exclamation-triangle"></i>
        </div>

        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default FactoryReset