import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import ScanVideo from '../../Assets/scan_video.mp4'

class ResistivityManual extends Component {

  constructor(props) {
    super(props)
    this.calibration = 0
    this.state = {
      cursorY: 0,
      selectedButtonName: 'A',
      content: 0,
      sensorValue: 0
    }

  }

  lines = ['A', 'B', 'C', 'D', 'E', 'F']


  componentDidMount() {
    this.calibration = localStorage.getItem("resCalibration")
    console.log(this.calibration)
    setTimeout(() => {
      this.refs.ResistivityManual.style.opacity = "1"
      this.refs.ResistivityManual.style.transform = "scale(1)"
    }, 10)
  }

  handleKeyDown = (socketData) => {
    let tempCursorY = this.state.cursorY
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'ok':
        this.setState({
          content: this.state.content + 1
        })

        if (this.state.content == 0) {
          this.refs.manualIntro.style.display = "block"
          this.refs.lines.style.display = "none"
          this.refs.manualScan.style.display = "none"
        }
        else if (this.state.content == 1) {
          this.refs.manualIntro.style.display = "none"
          this.refs.lines.style.display = "block"
          this.refs.manualScan.style.display = "none"
        }
        else if (this.state.content == 2) {
          this.refs.manualIntro.style.display = "none"
          this.refs.lines.style.display = "none"
          this.refs.manualScan.style.display = "flex"
          let wid = 0
          let preload = setInterval(() => {
            wid += 10
            this.refs.manualPreloaderSolid.style.width = wid + "%"
            this.getResData(this.state.selectedButtonName)
            if (wid > 110) {
              if (wid > 80) {
                this.getResData("Z")
              }
              clearInterval(preload)
              let payload = {
                value: this.state.sensorValue
              }
              this.props.getResistivityData(payload)
              this.props.navigateTo("resistivityManualResultScreen")
            }
          }, 300);
        }


        break
      case 'back':
        if (this.state.content == 2) {
          return
        }
        this.props.navigateTo("menuScreen")
        return
      case 'up':
        tempCursorY--
        break
      case 'down':
        tempCursorY++
        break
      default:
        break
    }

    const currentColumnSize = this.lines.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    this.setState({
      cursorY: tempCursorY,
      selectedButtonName: this.lines[tempCursorY]
    })

  }

  getResData = async (prop) => {
    let sensorResponse = await this.refs.socket.requestScan(prop)
    if(sensorResponse.type === "scan"){
      sensorResponse.payload =  parseInt(sensorResponse.payload) + parseInt(this.calibration)
      this.setState({
        sensorValue: sensorResponse
      })
    }
    console.log(sensorResponse)
  }



  render() {
    return (
      <div className="resistivity-manual" ref="ResistivityManual">
        <div ref="manualIntro">
          <div className="resistivity-intro-video"></div>
          <div className="resistivity-intro-text"> {this.props.strings["res_manual_warning"]} </div>
          <div className="start-button">{this.props.strings["select_line"]}</div>
        </div>


        <div className="resistivity-manual-scan-video" style={{ display: "none" }} ref="ScanVideoWrapper">
          <video src={ScanVideo} ref="ScanVideo" onEnded={this._onEnded}></video>
        </div>

        <div className="lines" style={{ display: "none" }} ref="lines">
          {
            this.lines.map((line, key) => {
              return <div key={key} className={`line ${this.state.selectedButtonName === line ? 'selected' : ''}`}>{line} line</div>
            })
          }
          <div id="one" className="props" />
          <div id="two" className="props" />
          <div id="three" className="props" />
          <div id="four" className="props" />

        </div>

        <div className="manual-scan" style={{ display: "none" }} ref="manualScan">
          <div className="line-scannig"> Line is scanning... </div>
          <div className="manual-preloader">
            <div ref="manualPreloaderSolid" className="manual-preloader-solid"></div>
          </div>
        </div>

        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default ResistivityManual