import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './Setup.css'
import Languages from '../../Strings.json'
import Timezones from '../../Timezones.json'
import Preloader from '../../Assets/preloader.png'

const languageKeys = Object.keys(Languages)
const regionKeys = Object.keys(Timezones)

class Setup extends Component {

  constructor(props) {
    super(props)
    this.state = {
      skipWelcome: false,
      panelIndex: 0,
      selectedRegion: '',
      helloText: "Hello"
    }
    this.panels = [
      {
        title: "setup_language",
        info: "setup_language_info",
        buttons: languageKeys,
        subCursor: languageKeys.length * 10,
        optionKey: "language"
      },
      {
        title: "setup_region",
        info: "setup_region_info",
        buttons: regionKeys,
        subCursor: regionKeys.length * 10,
        optionKey: "region"
      },
      {
        title: "city_timezone",
        info: "setup_city_info",
        buttons: [],
        subCursor: 1,
        optionKey: "timezone"
      },
      {
        title: "setup_unit",
        info: "setup_unit_info",
        buttons: ['metric', 'imperial'],
        subCursor: 1,
        optionKey: "unit"
      },
      {
        title: "menu_theme",
        info: "setup_theme_info",
        buttons: ["green", "orange", "blue"],
        subCursor: 3 * 10,
        optionKey: "theme"
      },
      {
        title: "3d_screen_background",
        info: "setup_3d_screen_info",
        buttons: ["black", "white", "parliament"],
        subCursor: 3 * 10,
        optionKey: "background_3d"
      },
      {
        title: "gps",
        info: "setup_gps_info",
        buttons: ["on", "off"],
        subCursor: 2 * 10,
        optionKey: "gps"
      },
    ]
  }


  componentDidMount() {
    console.log(localStorage.getItem('setupCompleted'))
    let hello = ['Hello', 'Merhaba', 'Bonjour', 'Hallo', 'Salve']
    let hello_index = 0
    this.interval = setInterval(() => {
      if (hello_index === hello.length - 1) {
        hello_index = 0
      }
      hello_index++
      this.setState({
        helloText: hello[hello_index]
      })
    }, 1500)
  }

  componentWillUnmount(){
    clearInterval(this.interval)
    clearTimeout(this.timeout)
  }



  welcomePanel = () => {
    this.timeout = setTimeout(() => {
      try {
        this.refs.stepper.style.display = 'flex'
      } catch (error) {
        
      }
      this.setState({
        skipWelcome: true
      })

    }, 15000)
    return (
      <div className="setup-panel">
        <div className="panel-welcome" >
          <div className="panel-welcome-title" ref="hello">{this.state.helloText}</div>
          <div className="panel-welcome-content">
            Welcome to Groundtech Discovery.
          </div>
          <div className="panel-welcome-content">
            Lets prepare your device's important settings together. It consists of 7 simple steps.
          </div>
          <img alt="preloader" className="setup-preloader" src={Preloader}></img>
          <div className="panel-welcome-content">
            Wait a moment.
          </div>
        </div>
      </div>
    )
  }

  renderPanel = (panel) => {
    // console.log(panel.buttons)
    let panelContent = () => {
      return (
        <div className="setup-panel">
          <div className="panel-title"> {this.props.strings[panel.title]}</div>
          <div className="panel-info"> {this.props.strings[panel.info]}</div>
          <div className="panel-selection">
            {
              ((panel.title !== "setup_region") && (panel.title !== 'city_timezone')) ?
                this.props.strings[(panel.buttons[panel.subCursor % panel.buttons.length])] :
                panel.buttons[panel.subCursor % panel.buttons.length]

            }
          </div>
        </div>
      )
    }

    return panelContent()
  }

  handleOkButton = () => {
    const currentPanel = this.panels[this.state.panelIndex]
    switch (currentPanel.title) {
      case 'setup_region':
        this.setState({
          selectedRegion: currentPanel.buttons[currentPanel.subCursor % currentPanel.buttons.length]
        }, function updateCityPanelButtons() {
          let cityPanel = this.panels[this.state.panelIndex + 1]
          cityPanel.buttons = Object.keys(Timezones[this.state.selectedRegion])
          cityPanel.subCursor = cityPanel.buttons.length * 10
        })
        break
      case 'city_timezone':
        let selectedCity = currentPanel.buttons[currentPanel.subCursor % currentPanel.buttons.length]
        let tz = Timezones[this.state.selectedRegion][selectedCity]
        console.log(selectedCity, Timezones[this.state.selectedRegion], tz)
        localStorage.setItem(currentPanel.optionKey, tz)
        this.props.setTheme({
          type: "timezone",
          data: tz
        })
        break
      case 'gps':
        this.props.navigateTo("menuScreen")
        break
      default:
        localStorage.setItem("setupCompleted", true)
        localStorage.setItem(currentPanel.optionKey, currentPanel.buttons[currentPanel.subCursor % currentPanel.buttons.length])
        this.props.setTheme({
          type: currentPanel.optionKey,
          data: currentPanel.buttons[currentPanel.subCursor % currentPanel.buttons.length]
        })
        break
    }
    this.setState({
      panelIndex: this.state.panelIndex + 1
    })
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }

    switch (socketData.payload) {
      case 'left':
        this.panels[this.state.panelIndex].subCursor--
        this.forceUpdate()
        break
      case 'right':
        this.panels[this.state.panelIndex].subCursor++
        this.forceUpdate()
        break
      case 'ok':
        this.handleOkButton()
        break
      case 'back':
        // this.props.navigateTo("menuScreen")
        if (this.state.panelIndex > 0) {
          this.setState({
            panelIndex: this.state.panelIndex - 1
          })
        }

        break
      default:
        break
    }
  }


  render() {
    return (
      <div className="setup">
        <div className="stepper" ref="stepper">

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 0) ? ' stepper-selected' : ''} ${(this.state.panelIndex > 0) ? ' stepper-passed' : ''} `}>1</div>
            <div className="name">Language</div>
          </div>

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 1) ? ' stepper-selected' : ''}  ${(this.state.panelIndex > 1) ? ' stepper-passed' : ''}`}>2</div>
            <div className="name">Region</div>
          </div>

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 2) ? ' stepper-selected' : ''} ${(this.state.panelIndex > 2) ? ' stepper-passed' : ''}`}>3</div>
            <div className="name">City</div>
          </div>

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 3) ? ' stepper-selected' : ''} ${(this.state.panelIndex > 3) ? ' stepper-passed' : ''}`}>4</div>
            <div className="name">Unit</div>
          </div>

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 4) ? ' stepper-selected' : ''} ${(this.state.panelIndex > 4) ? ' stepper-passed' : ''}`}>5</div>
            <div className="name">Theme</div>
          </div>

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 5) ? ' stepper-selected' : ''} ${(this.state.panelIndex > 5) ? ' stepper-passed' : ''}`}>6</div>
            <div className="name">3D Background</div>
          </div>

          <div className="stepper-item">
            <div className={`index ${(this.state.panelIndex === 6) ? ' stepper-selected' : ''} ${(this.state.panelIndex > 6) ? ' stepper-passed' : ''}`}>7</div>
            <div className="name">GPS</div>
          </div>
        </div>
        {
          this.state.skipWelcome ? this.renderPanel(this.panels[this.state.panelIndex]) : this.welcomePanel()
          // this.renderPanel(this.panels[this.state.panelIndex])
        }




        {/* <div className="panel-welcome">
                        <div className="panel-welcome-title">HELLO</div>
                        <div className="panel-welcome-content">
                            Welcome to Groundtech Discovery.
                        </div>
                        <div className="panel-welcome-content">
                            Lets prepare your device's important settings together. It consists of 6 simple steps.
                        </div>
                        <div className="panel-welcome-content">
                            Press 'OK' button to continue.
                        </div>
                    </div> */}
        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}
export default Setup