import React, { Component } from 'react'
import './Battery.css'

class Battery extends Component {
  constructor(props) {
    super(props)

    this.colors = [
      "#ff0000",
      "#ff2a00",
      "#ff5400",
      "#ff8300",
      "#ffc700",
      "#fff200",
      "#e1ff00",
      "#8cff00",
      "#4cff00",
      "#04ff00",
      "#04ff00"
    ]
  }

  componentDidMount() {

  }

  componentDidUpdate() {
    this.refs.levelIndicator.style.width = this.props.level + "%"
    this.refs.levelIndicator.style.background = this.colors[Math.trunc((this.props.level) / 10)]
    this.refs.charging.style.display = this.props.charging
  }

  render() {
    return (
      <div className="battery">
        <div className="battery-level">{(!isNaN(this.props.level) ? this.props.level : "")}%</div>
        <div className="battery-outer">
          <i ref="charging" className="fas fa-bolt"></i>
          <div ref="levelIndicator" className="battery-inner"></div>
        </div>
      </div>
    )
  }
}

export default Battery