import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import * as THREE from "three";

import './ResistivityAutoResult.css'
import Colours from './Colours.json'

class ResistivityAutoResult extends Component {
  constructor(props) {
    super(props);
    this.matrix = []
    this.results = null
    this.initializeCamera = this.initializeCamera.bind(this);
    this.state = {
      cursorY: 100,
      popup: false
    }


  }

  componentDidMount() {
    console.log(this.props.result)
    this.results = this.props.result

    const width = this.refs.canvasHolder.clientWidth;
    const height = this.refs.canvasHolder.clientHeight;
    this.scene = new THREE.Scene();
    var aspect = width / (height)
    let viewsize = 10;
    // this.camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 1000);
    this.camera = new THREE.OrthographicCamera(viewsize * aspect / - 2, viewsize * aspect / 2, viewsize / 2, viewsize / - 2, -100, 100);
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setSize(width, height);
    this.refs.canvasHolder.appendChild(this.renderer.domElement);
    this.initializeCamera();

    this.SetColor(this.matrix, parseInt(this.results.A), parseInt(this.results.B), parseInt(this.results.C), parseInt(this.results.D), parseInt(this.results.E), parseInt(this.results.F))

    let e_matrix = this.interpolate(this.matrix, 7)
    let flatmatrix = []

    e_matrix.forEach(element => {
      element.forEach(val => {
        flatmatrix.push(val)
      })
    });

    this.CreateGridPlane()


    const geometry = new THREE.PlaneGeometry(6, 6, e_matrix.length - 1, e_matrix[0].length - 1);
    // const geometry = new THREE.BoxGeometry(1, 1, e_matrix.length - 1);
    this.material = new THREE.MeshBasicMaterial({ color: 0xFFFFFF, wireframe: false, side: THREE.DoubleSide, vertexColors: THREE.FaceColors });
    this.cube = new THREE.Mesh(geometry, this.material);


    flatmatrix.map((element, index) => {
      geometry.vertices[index].z = element / 250 * -1
    });


    let sutun = 0
    let satir = 0
    let en = e_matrix.length;
    for (let i = 0; i < geometry.faces.length; i += 2) {
      satir = Math.trunc(i / ((en - 1) * 2))
      sutun = Math.trunc((i % ((en - 1) * 2)) / 2)

      // i. face
      let face = geometry.faces[i]
      face.vertexColors[0] = this.colorFromArray(e_matrix[satir][sutun])
      face.vertexColors[2] = this.colorFromArray(e_matrix[satir][sutun + 1])
      face.vertexColors[1] = this.colorFromArray(e_matrix[satir + 1][sutun])
      // i+1 face
      face = geometry.faces[i + 1]
      face.vertexColors[0] = this.colorFromArray(e_matrix[satir + 1][sutun])
      face.vertexColors[1] = this.colorFromArray(e_matrix[satir + 1][sutun + 1])
      face.vertexColors[2] = this.colorFromArray(e_matrix[(satir)][sutun + 1])
    }



    this.scene.add(this.cube);
    this.animate();


    setTimeout(() => {
      this.refs.preloader.style.transform = "translateY(-500px)"
    }, 1500);


  }

  colorFromArray = (val) => {
    let mapped = (val / 1023) * 474
    return new THREE.Color(Colours[Math.trunc(mapped)])
  }

  rawColorFromArray = (val) => {
    let mapped = (val / 1023) * 474
    return Colours[Math.trunc(mapped)]
  }

  SetColor = (m, a, b, c, d, e, f) => {
    m.push([(a + d) / 2, a, a, a, (a + b) / 2])
    m.push([d, e, a, f, b])
    m.push([d, d, (e + f) / 2, b, b])
    m.push([d, f, c, e, b])
    m.push([(c + d) / 2, c, c, c, (b + c) / 2])


    this.refs.Aline.style.background = this.rawColorFromArray(a)
    this.refs.Bline.style.background = this.rawColorFromArray(b)
    this.refs.Cline.style.background = this.rawColorFromArray(c)
    this.refs.Dline.style.background = this.rawColorFromArray(d)
    this.refs.Eline.style.background = this.rawColorFromArray(e)
    this.refs.Fline.style.background = this.rawColorFromArray(f)

    this.refs.Adata.innerHTML = this.props.strings[this.GetType(a)] + " (" + a + ")"
    this.refs.Bdata.innerHTML = this.props.strings[this.GetType(b)] + " (" + b + ")"
    this.refs.Cdata.innerHTML = this.props.strings[this.GetType(c)] + " (" + c + ")"
    this.refs.Ddata.innerHTML = this.props.strings[this.GetType(d)] + " (" + d + ")"
    this.refs.Edata.innerHTML = this.props.strings[this.GetType(e)] + " (" + e + ")"
    this.refs.Fdata.innerHTML = this.props.strings[this.GetType(f)] + " (" + f + ")"


  }


  GetType = (val) => {
    switch (true) {
      case (val < 20):
        return "notConnect"
      case (val < 105):
        return "cavity"
      case (val < 294):
        return "ground"
      case (val < 440):
        return "water"
      case (val < 490):
        return "mineral"
      case (val < 605):
        return "metal"
      case (val < 1023):
        return "short"
      default:
        break;
    }
  }

  CreateGridPlane = () => {
    var gridgeometry = new THREE.PlaneGeometry(7.2, 7.2, 1, 1);
    var floorTexture = new THREE.ImageUtils.loadTexture("http://localhost:3000/lines2.png");
    floorTexture.wrapS = floorTexture.wrapT = THREE.RepeatWrapping;
    floorTexture.repeat.set(1, 1);
    var gridmaterial = new THREE.MeshBasicMaterial({ map: floorTexture, side: THREE.DoubleSide, transparent: true, wireframe: false });
    var gridplane = new THREE.Mesh(gridgeometry, gridmaterial);
    gridplane.position.z = 0.01
    this.scene.add(gridplane);

  }

  interpolate = (data, factor) => {
    var level = factor
    var i_row = []
    var interpolated_array = []
    var interpolated_rows = []
    //2D iterpolation for x axis
    for (var k = 0; k < data.length; k++) {
      i_row.push(data[k][0])
      for (var i = 0; i < data[k].length - 1; i++) {
        var fark = data[k][i] - data[k][i + 1]
        for (var y = 0; y < level; y++) {
          i_row.push(parseInt(data[k][i] - (fark / (level + 1) * (y + 1))))
        }
        i_row.push(data[k][i + 1])
      }
      interpolated_array.push(i_row)
      i_row = []
    }
    //2D iterpolation for y axis
    for (var i = 0; i < interpolated_array.length - 1; i++) {
      for (var k = 0; k < level; k++) {
        for (var j = 0; j < interpolated_array[0].length; j++) {
          var fark = interpolated_array[i][j] - interpolated_array[i + 1][j]
          i_row.push(parseInt(interpolated_array[i][j] - (fark / (level + 1) * (k + 1))))
          if (interpolated_array[0].length - 1 == j) {
            interpolated_rows.push(i_row)
            var i_row = []
          }
        }
      }
      i_row = []
    }
    var result = []
    var j = 0
    for (let i = 0; i < interpolated_array.length - 1; i++) {
      result.push(interpolated_array[i])
      for (j; j < ((i + 1) * level); j++) {
        result.push(interpolated_rows[j])
      }
    }
    result.push(interpolated_array[interpolated_array.length - 1])
    return result
  }

  componentWillUnmount() {
    localStorage.setItem("resJustScanned", "false")
    cancelAnimationFrame(this.frameId);
    this.refs.canvasHolder.removeChild(this.renderer.domElement);

  }

  saveResData = async () => {
    let sensorResponse = await this.refs.socket.requestScan("RES" + JSON.stringify(this.results))
    console.log(sensorResponse)
  }

  initializeCamera() {
    this.camera.position.x = 0;
    this.camera.position.y = 0;
    this.camera.zoom = 1.3;
    this.camera.updateProjectionMatrix();
  }

  animate = () => {
    this.frameId = window.requestAnimationFrame(this.animate);
    this.renderer.render(this.scene, this.camera);

  }

  addCube = (cube) => {
    cube.translateZ(2);
    this.scene.add(cube);
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let rotateAngle = 0.04
    switch (socketData.payload) {
      case 'left':
        if (this.state.popup) {
          this.setState({
            cursorY: this.state.cursorY - 1
          })
        } else {
          for (let i = 0; i < 10; i++) {
            setTimeout(() => {
              this.camera.rotation.y += rotateAngle
            }, 20 * i);
          }
        }
        break
      case 'right':
        if (this.state.popup) {
          this.setState({
            cursorY: this.state.cursorY + 1
          })
        } else {
          for (let i = 0; i < 10; i++) {
            setTimeout(() => {
              this.camera.rotation.y -= rotateAngle
            }, 20 * i);
          }
        }
        break
      case 'up':
        for (let i = 0; i < 10; i++) {
          setTimeout(() => {
            this.camera.rotation.x += rotateAngle
          }, 20 * i);
        }
        break
      case 'down':
        for (let i = 0; i < 10; i++) {
          setTimeout(() => {
            this.camera.rotation.x -= rotateAngle
          }, 20 * i);
        }
        break
      case 'start':
        this.material.wireframe = !this.material.wireframe

        break
      case 'ok':
        if(this.state.popup){
          if(this.state.cursorY % 2 == 0){
            this.saveResData()
            setTimeout(() => {
              console.log("saved")
              this.props.navigateTo("resFileBrowser")
            }, 100);
          }else{
            this.props.navigateTo("resistivityAuto")
          }
        }
        this.material.wireframe = !this.material.wireframe

        break
      case 'back':
        this.setState({
          popup: !this.state.popup
        })
        // this.props.navigateTo("menuScreen")
        return
      default:
        break
    }

  }

  componentDidUpdate() {
    if (localStorage.getItem("resJustScanned") == "true") {
      if (this.state.popup === true) {
        this.refs.resPopup.style.display = "flex"
      } else {
        this.refs.resPopup.style.display = "none"
      }
    }else{
      this.props.navigateTo("menuScreen")
    }

  }

  render() {
    return (
      <div className="auto-result">
        <div ref="preloader" className="res-preloader"> Loading 3D Resistivity Results </div>

        <div ref="resPopup" className="res-save-popup-container">
          <div className="res-save-popup">
            <i className="fas fa-save"></i>
            <span>Do you want to save this scan?</span>
            <div className="res-popup-buttons">
              <div className={`res-button ${(this.state.cursorY % 2 == 0) ? "selected" : ""}`}> Yes </div>
              <div className={`res-button ${(this.state.cursorY % 2 == 1) ? "selected" : ""}`}> No </div>
            </div>
          </div>
          {/* <div className="popup-overlay"></div> */}
        </div>

        <div className="lines-container">
          <div className="line"> <div className="line-label">A</div> <div className="line-data" ref="Adata">Metal</div> <div ref="Aline" className="indicator"></div></div>
          <div className="line"> <div className="line-label">B</div> <div className="line-data" ref="Bdata">Cavity</div> <div ref="Bline" className="indicator"></div></div>
          <div className="line"> <div className="line-label">C</div> <div className="line-data" ref="Cdata">Cavity</div><div ref="Cline" className="indicator"></div> </div>
          <div className="line"> <div className="line-label">D</div> <div className="line-data" ref="Ddata">None</div> <div ref="Dline" className="indicator"></div></div>
          <div className="line"> <div className="line-label">E</div> <div className="line-data" ref="Edata">None</div> <div ref="Eline" className="indicator"></div></div>
          <div className="line"> <div className="line-label">F</div> <div className="line-data" ref="Fdata">None</div> <div ref="Fline" className="indicator"></div></div>


        </div>

        <div className="canvas-container" ref="canvasHolder">  </div>
        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default ResistivityAutoResult