import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './TurnOff.css'

class TurnOff extends Component {

  constructor(props) {
    super(props)
    this.state = {
      cursorX: 0,
      cursorY: 0,
      selectedButtonName: "Yes"
    }

    this.buttons = [
      "Yes", "No"
    ]

  }


  titre = async () => {
      let sensorResponse = await this.refs.socket.requestTitre()
  }

  componentDidMount(){
    setTimeout(() => {
      this.titre()
     
      this.refs.turnOff.style.transform= "scale(1)"
      this.refs.turnOff.style.opacity = 1      
    }, 130);

    // if(!localStorage.getItem("turnOffCount")){
    //   localStorage.setItem("turnOffCount", 0)
    // }

  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursorY = this.state.cursorY
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'left':
        tempCursorY--
        break
      case 'right':
        tempCursorY++
        break
      case 'ok':
        if (this.state.cursorY === 0) {
          
          localStorage.setItem("turnOffCount", parseInt(localStorage.getItem("turnOffCount")) + 1)
          setTimeout(() => {
            this.props.navigateTo("turningOffScreen")
          }, 1200);
        } else if (this.state.cursorY === 1) {
          this.props.navigateTo("menuScreen")
        }

        return
      case 'back':
        this.props.navigateTo("menuScreen")
        return
      default:
        break
    }

    const currentColumnSize = this.buttons.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    const currentRowSize = this.buttons.length

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      cursorY: tempCursorY,
      selectedButtonName: this.buttons[tempCursorY],
    })
  }


  render() {
    return (
      <div className="turn-off" ref="turnOff">
        <div className="turn-off-popup">
          <i className="fas fa-power-off"></i>
          <div className="turn-off-question">
            Groundtech Discovery is about to shut down. Are you sure?
          </div>
          <div className="turn-off-button-container">
            {
              this.buttons.map((button, key) => {
                return (
                  <div key={key} className={`turn-off-button${(button === this.state.selectedButtonName) ? ' selected' : ''}`}>{button}</div>
                )
              })
            }
          </div>

        </div>
        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }

}


export default TurnOff