import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './HardwareInfo.css'
// import Sprite from '../../Assets/spritesheetdiscovery.png'
import icon_sensor from '../../Assets/hardware/sensor.svg'
import icon_cpu from '../../Assets/hardware/cpu.svg'
import icon_gps from '../../Assets/hardware/gps.svg'
import icon_hdd from '../../Assets/hardware/hdd.svg'
import icon_ram from '../../Assets/hardware/ram.svg'
import icon_resist from '../../Assets/hardware/resist.svg'
import icon_signal from '../../Assets/hardware/signal.svg'
import icon_network from '../../Assets/hardware/network.svg'

class HardwareInfo extends Component {


  constructor(props) {
    super(props)

    this.state = {
      tabIndex: 9,
      status: {
        cpu: 50,
        ram: 50,
        hdd: 50,
        temp: 50
      }
    }

    let getStatusInterval
  }

  componentDidMount() {
    this.getStatusInterval = setInterval(() => {
      this.getStatus()
    }, 1500);
  }

  componentWillUnmount() {
    clearInterval(this.getStatusInterval)
  }

  renderHardwareInfo = () => {
    return (
      <div className="hardware-info-tab">
        <div className="info-item">
          <div className="info-title">Sensor</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_sensor}></img></div>
          <div className="info-value"> UMAD ETS</div>
        </div>

        <div className="info-item">
          <div className="info-title">Resistivity</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_resist}></img></div>
          <div className="info-value"> 110V</div>
        </div>

        <div className="info-item">
          <div className="info-title">CPU</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_cpu}></img></div>
          <div className="info-value"> 1.6 GHz </div>
        </div>

        <div className="info-item">
          <div className="info-title">Ram</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_ram}></img></div>
          <div className="info-value"> 512 MB</div>
        </div>

        <div className="info-item">
          <div className="info-title">Storage</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_hdd}></img></div>
          <div className="info-value"> 16 GB</div>
        </div>

        <div className="info-item">
          <div className="info-title">Wifi</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_network}></img></div>
          <div className="info-value"> 2.4 GHz </div>
        </div>

        <div className="info-item">
          <div className="info-title">RF</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_signal}></img></div>
          <div className="info-value"> 433 MHz</div>
        </div>

        <div className="info-item">
          <div className="info-title">Gps</div>
          <div className="info-icon-holder"><img className="info-icon" src={icon_gps}></img></div>
          <div className="info-value"> Yes</div>
        </div>

      </div>
    )
  }

  renderSystemStatus = () => {
    return (
      <div className="system-status-tab">

        <div className="status">
          <div className="status-title"> CPU </div>
          <div className="status-graphic">
            <div className="status-line">
              <div className="line-value" style={{height: this.state.status.cpu + "%"}}>
                <div className="line-indicator">
                  %{this.state.status.cpu}
                </div>
              </div>
            </div>
          </div>
          <div className="status-value"> {1500 * this.state.status.cpu/ 100} MHz </div>
        </div>

        <div className="status">
          <div className="status-title"> Ram </div>
          <div className="status-graphic">
            <div className="status-line">
              <div className="line-value"  style={{height: this.state.status.ram + "%"}}>
                <div className="line-indicator">
                  %{this.state.status.ram }
                </div>
              </div>
            </div>
          </div>
          <div className="status-value"> {Math.trunc(512 * this.state.status.ram / 100)} MB </div>
        </div>

        <div className="status">
          <div className="status-title"> Storage </div>
          <div className="status-graphic">
            <div className="status-line">
              <div className="line-value"  style={{height: this.state.status.hdd + "%"}}>
                <div className="line-indicator">
                  %{this.state.status.hdd}
                </div>
              </div>
            </div>
          </div>
          <div className="status-value"> { 15 * this.state.status.hdd / 100} GB </div>
        </div>

        <div className="status">
          <div className="status-title"> Temp </div>
          <div className="status-graphic">
            <div className="status-line">
              <div className="line-value"  style={{height: this.state.status.temp + "%"}}>
                <div className="line-indicator">
                {this.state.status.temp }
                </div>
              </div>
            </div>
          </div>
          <div className="status-value"> {this.state.status.temp } °C</div>
        </div>


      </div>
    )
  }

  getStatus = async () => {
    let sensorResponse = await this.refs.socket.requestScan("status")
    this.setState({
      status: sensorResponse
    })
    
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'left':
        this.setState({
          tabIndex: (this.state.tabIndex - 1)
        })
        console.log("asd")
        break
      case 'right':
        this.setState({
          tabIndex: (this.state.tabIndex + 1)
        })

        break
      case 'ok':

        return
      case 'back':
        this.props.navigateTo("menuScreen")
        break
      default:
        break
    }
  }

  render() {
    return (
      <div className="hardware-info">
        <div className="tabs-container">
          <div className={`tab ${(this.state.tabIndex % 2 == 0) ? ' selected-tab' : ''}`}>Hardware</div>
          <div className={`tab ${(this.state.tabIndex % 2 == 1) ? ' selected-tab' : ''}`}>System Status</div>
        </div>
        <div className="hardware-info-container">
          {
            (this.state.tabIndex % 2 == 0)? this.renderHardwareInfo() : this.renderSystemStatus()
          }
        </div>

        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default HardwareInfo