import React, { Component } from 'react'
import "./Scanning.css"
import SocketHelper from '../../SocketHelper'


class Scanning extends Component {

  constructor(props) {
    super(props)
    this.exitCheck = 0;
  }


  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'down':

        break
      case 'up':
        console.log("upupupupupup")
        break
      case 'left':
        break
      case 'right':

        break
      case 'ok':
        // this.props.groundNavigateTo("scanningScreen")
        break
      case 'back':
        this.exitCheck++;
        if (this.exitCheck === 20) {
          this.props.groundNavigateTo("menuScreen") //UNUTMA: bu noktada iframeden gelen mesaja göre çıkış yapacağız.
        }
        return
      default:
        break
    }

  }

  componentDidMount() {
    window.addEventListener('message', (event) => {
      if (event.data.event_id === 'my_cors_message') {
        console.log(event.data.data);
        if (event.data.data === "exittoscan") {
          this.props.navigateTo("menuScreen")
        }
        if (event.data.data === "save") {
          this.props.navigateTo("fileBrowser")
        }
        if (event.data.data === "exittomenu") {
          this.props.navigateTo("menuScreen")
        }
      }
    });
  }

  componentWillUnmount() {
    //   window.removeEventListener('message')
  }

  // this.scanOptions = {
  //   "scan_type": this.buttons[0].options[this.buttons[0].subCursor % this.buttons[0].options.length],
  //   "pattern": this.buttons[1].options[this.buttons[1].subCursor % this.buttons[1].options.length],
  //   "width_step": this.buttons[2].options[this.buttons[2].subCursor % this.buttons[2].options.length],
  //   "height_step": this.buttons[3].options[this.buttons[3].subCursor % this.buttons[3].options.length],
  //   "scan_sense": this.buttons[4].options[this.buttons[4].subCursor % this.buttons[4].options.length],
  //   "duration": this.buttons[5].options[this.buttons[5].subCursor % this.buttons[5].options.length],
  //   "direction": this.buttons[6].options[this.buttons[6].subCursor % this.buttons[6].options.length],
  // }


  serializeScanOptions = () => {
    let stepSize = 30
    //#auto#en#boy#süre#zigzag#left
    const s = this.props.scanOptions
    let scanOptionsString;
    let vibration = localStorage.getItem("vibration")
    let language = localStorage.getItem("language")
    console.log(language)

    if (s.scan_type === 'metre') {
      scanOptionsString = `#${s.scanMode}#${Math.trunc(s.width_metre * 100 / stepSize)}#${Math.trunc(s.height_metre * 100 / stepSize)}#${s.duration * 1000}#${s.pattern}#${s.direction}#${vibration}#${language}`
    } else {
      scanOptionsString = `#${s.scanMode}#${s.width_step}#${s.height_step}#${s.duration * 1000}#${s.pattern}#${s.direction}#${vibration}#${language}`
    }
    console.log(scanOptionsString)

    return scanOptionsString
  }


  render() {
    let scanOptionsString = this.serializeScanOptions()
    return (
      <div className="scanning">
        <iframe ref="threed" className="threed" title="3d" onLoad={this.hideSpinner} src={`http://localhost:8080/scaning/${scanOptionsString}`}></iframe>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default Scanning