import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import blue from '../../Assets/themes/blue.jpg'
import green from '../../Assets/themes/green.jpg'
import orange from '../../Assets/themes/orange.jpg'

import './BackgroundColor.css'
import './Settings.css'

class BackgroundColor extends Component {

  
  constructor(props) {
    super(props)
    this.state = {
      cursorX: 0,
      cursor_3d: 0,
      cursor_scan: 0,
      selected3d: "black",
      selectedScan: "black"
    }
  }
  
  timer = 0


  colors = [
    {
      "name": "black",
      "image": green

    },
    {
      "name": "white",
      "image": blue

    },
    {
      "name": "gray",
      "image": orange

    }
  ]

  componentDidMount() {
    setTimeout(() => {
      this.refs.settings.style.opacity = "1";
      this.refs.settings.style.transform = "scale(1)";
    }, 10)
  }
  
  componentWillUnmount(){
    clearTimeout(this.timer)
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursor_3d = this.state.cursor_3d
    let tempCursor_scan = this.state.cursor_scan
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'left':
        if (this.state.cursorX === 0) {
          tempCursor_3d--
        } else if (this.state.cursorX === 1) {
          tempCursor_scan--
        }
        break
      case 'right':
        if (this.state.cursorX === 0) {
          tempCursor_3d++
        } else if (this.state.cursorX === 1) {
          tempCursor_scan++
        }
        break
      case 'up':
        tempCursorX--
        break
      case 'down':
        tempCursorX++
        break
      case 'ok':
        this.props.setTheme({
          type: "background",
          data: this.colors[this.state.cursor_3d].name
        },{
          type: "background",
          data: this.colors[this.state.cursor_scan].name
        })

        this.refs.confirm.style.display = "flex"
        this.timer = setTimeout(() => {
          this.refs.confirm.style.display = "none"
          this.props.navigateTo("menuScreen")
          return
        }, 1500);
        break
      case 'back':
        this.props.navigateTo("menuScreen")
        return
      default:
        break
    }



    const currentColumnSize = this.colors.length

    if (tempCursor_3d < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursor_3d = lastButtonOnColumn
    } else if (tempCursor_3d > currentColumnSize - 1) {
      tempCursor_3d = 0
    }

    if (tempCursor_scan < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursor_scan = lastButtonOnColumn
    } else if (tempCursor_scan > currentColumnSize - 1) {
      tempCursor_scan = 0
    }

    const currentRowSize = 2

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      selected3d: this.colors[tempCursor_3d].name,
      selectedScan: this.colors[tempCursor_scan].name,
      cursorX: tempCursorX,
      cursor_3d: tempCursor_3d,
      cursor_scan: tempCursor_scan
    })
  }


  render() {
    return (
      <div className="settings" ref="settings">
        <div className="background-color">
          <div className="confirm" ref="confirm" style={{display: "none"}}>  <i className="fas fa-check"></i> <span>Your settings is changed successfuly.</span></div>
          <div className="title">Background Color Settings</div>
          <div className="info"> Select background color for Scanning and 3D Viewer screens. </div>
          <div className="select-container">
            <div className="select-title">3D Viewer Background</div>
            <div className={`select${(this.state.cursorX % 2 === 0) ? ' selected' : ''}`}>{this.colors[this.state.cursor_3d].name}</div>
            <div className="select-title">Scanning Background</div>
            <div className={`select${(this.state.cursorX % 2 === 1) ? ' selected' : ''}`} >{this.colors[this.state.cursor_scan].name}</div>
          </div>

        </div>
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default BackgroundColor