import React, { Component } from 'react'
import SocketHelper from '../../SocketHelper'
import './SoftwareInfo.css'
import Groundtech from '../../Assets/groundtech_logo.png'
import axios from 'axios'

const COUNTER_URL = "http://localhost:8080/counter"

class SoftwareInfo extends Component {


  constructor(props) {
    super(props)

    this.state = {
      counter: 0,
      leftButtonCounter: 0
    }
  }


  componentDidMount(){
    console.log("mounted")
    this.getCounter()
  }

  getCounter = () => {
    try {
      axios.get(COUNTER_URL)
        .then(response => {
          this.setState({
            counter: response.data
          })
        })
    } catch (error) {
      console.log("error while getting sn number: " + error)
    }
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'left':
        this.setState({
          leftButtonCounter: this.state.leftButtonCounter + 1
        })
        if(this.state.leftButtonCounter > 8){
          this.refs.Counter.style.display = "block"
          setTimeout(() => {
            this.refs.Counter.style.display = "none"
          }, 2000);
          this.setState({
            leftButtonCounter: 0
          })
        }
        break
      case 'right':

        break
      case 'ok':

        return
      case 'back':
        this.props.navigateTo("menuScreen")
        break
      default:
        break
    }
  }

  render() {
    return (
      <div className="software-info">
        <span style={{fontWeight: 600, fontSize: 20}}>Groundtech GtOS embedded </span>
        <span style={{fontSize: 13}}>V2.0 build: 2-2130</span>
        <img className="groundtech-logo" src={Groundtech}/>
        <div className="counter" ref="Counter">
          Device boot counter: {this.state.counter}
        </div>

        <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default SoftwareInfo