import React, { Component } from 'react'
import './LiveScan.css'
import SocketHelper from '../../SocketHelper'

class LineScan extends Component {

  constructor(props) {
    super(props)
    this.x = 460;
    this.y = 87;
    this.level = 0;
    this.i = 1;
    this.pos = null
    this.gecmis = []
    this.info = "You can change the data rate by pressing + and - buttons."
    this.state = {
      total: 0,
      average: 0,
      sensorRate: 300, // TODO: HIZLI ORTA YAVAŞ
      scanActive: false
    }
  }

  componentDidMount() {
    this.canvas = this.refs.canvas
    this.ctx = this.canvas.getContext('2d');
    this.ctx.lineWidth = 5
    this.ctx.lineCap = 'round'
    this.ctx.lineJoin = 'round'
    this.draw(this.x, this.y, this.level);
    this.setState({ scanActive: true })


    this.sensorInterval = setTimeout(this.myFunction, this.state.sensorRate)


  }

  myFunction = () => {
    this.getSensorData()
    setTimeout(this.myFunction, this.state.sensorRate)
  }

  getSensorData = async () => {
    if (!this.state.scanActive) { return }
    console.log(this.y)
    let sensorResponse = await this.refs.socket.requestScan("Q")
    if (sensorResponse.type === "sensor") {
      this.y = parseInt(sensorResponse.payload)

      if (this.i < 10) {
        this.setState({ total: this.state.total + this.y })
        this.i++
      } else {
        this.refs.averageCalc.style.display = "none"
        if (this.y > this.state.average) {
          this.level = -(this.y - this.state.average) / (255 - this.state.average)
        } else {
          this.level = +(this.state.average - this.y) / this.state.average
        }
      }
    }

    this.setState({ average: this.state.total / 9 })
    this.draw(this.x, 87 + (this.level * 86), this.level);
  }

  drawLine(start, end, c) {
    c.beginPath();
    c.moveTo(start.x, start.y);
    c.lineTo(end.x, end.y);
    c.stroke();
    c.closePath();
  }

  draw(x, y, color) {
    this.ctx.clearRect(0, 0, this.canvas.clientWidth, this.canvas.clientHeight)
    this.gecmis.push({ x, y, color })
    this.gecmis.forEach((element, key) => {
      this.ctx.strokeStyle = 'hsl(' + ((115) + (element.color * 114)) + ',100%,50%)';
      let next = this.gecmis[key + 1]
      if (next) {
        this.drawLine({ x: element.x, y: element.y }, { x: next.x, y: next.y }, this.ctx)
      }
      element.x -= 18;

      if (element.x < -12) {
        this.gecmis.shift(element)
      }
    })

  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'back':
        if (this.state.scanActive) {
          this.setState({ scanActive: false })
          clearTimeout(this.sensorInterval)
          this.props.navigateTo("menuScreen")
        }
        return
      case 'zoomout':
        this.setState({sensorRate: this.state.sensorRate + 200})
        break
      case 'zoomin':
        this.setState({sensorRate: this.state.sensorRate - 200})
        break
      default:
        break
    }
  }

  render() {
    return (
      <div className="line-scan">
        <div className="average-calc" ref="averageCalc">calculating dirt level</div>
        <canvas ref="canvas" id="canvas" width={400} height={170}></canvas>
        <div className="grid-bg"></div>
        <div className="info">{this.state.sensorRate}</div>
        <SocketHelper ref='socket' onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default LineScan