import React, { Component } from 'react'
import './TurningOff.css'
import SocketHelper from '../../SocketHelper'

class TurningOff extends Component{

  componentDidMount(){
    setTimeout(() => {
      this.refs.socket.requestTurnOff()
    }, 3000);

    setTimeout(() => {
      this.refs.turningOff.style.background = "black"
    }, 2000);
  }

  render(){
    return(
      <div className="turning-off" ref="turningOff">
          <i className="fas fa-power-off"></i>
          <div className="rotating-ring"></div>
          <SocketHelper ref="socket" onMessage={this.handleKeyDown} />
      </div>
    )
  }

}

export default TurningOff