import React, { Component } from 'react'
import { QRCode } from "react-qr-svg";
import './Activation.css'
import axios from 'axios'
import SocketHelper from '../../SocketHelper'

const SN_URL = "http://localhost:8080/sn"

class Activation extends Component {

  constructor(props) {
    super(props)

    this.state = {
      sn: ""
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.refs.activation.style.opacity = 1;
      this.refs.activation.style.transform = "scale(1)";
    }, 100);

    this.getSn()
  }

  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    switch (socketData.payload) {
      case 'left':
        break
      case 'right':
        break
      case 'ok':
        break
      case 'back':
        this.props.navigateTo("menuScreen")
        return
      default:
        break
    }
  }

  getSn = () => {
    try {
      axios.get(SN_URL)
        .then(response => {
          this.setState({
            sn: response.data
          })
        })
    } catch (error) {
      console.log("error while getting sn number: " + error)
    }
  }

  renderQRCode = () => {
    return (
      <QRCode
        bgColor="#FFFFFF"
        fgColor="#000000"
        level="Q"
        style={{ width: 150 }}
        value={this.state.sn.slice(-8).toUpperCase()}
      />
    )
  }

  render() {
    return (
      <div className="activation" ref="activation">
        <div className="device-id-info">Your Groundtech Discovery SN</div>
        <div className="device-id">{this.state.sn.slice(-8).toUpperCase()}</div>

        {
          (this.state.sn !== "") ? this.renderQRCode() : ''
        }


        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default Activation