import React, { Component } from 'react'
import './Viewer.css'
import SocketHelper from '../../SocketHelper'

class Viewer extends Component {

  constructor(props) {
    super(props)
    
    this.file = "http://localhost:8080/3d-graphic-code#" + this.props.open
    this.buttons = [
      {
        name: "12032019-1256"
      },
      {
        name: "25022019-0836"
      },
      {
        name: "03122018-1827"
      },
    ]

    this.state = {
      cursorX: 0,
      cursorY: 0,
      loading: true
    }
  }

  hideSpinner = () => {
    setTimeout(() => {
      this.setState({
        loading: false
      })
      // this.refs.overlay.style.display = "block"
    }, 0)
  }


  // componentDidMount() {
   
  // }

  componentDidMount() {
    console.log(this.file)
    window.addEventListener('message', (event) => {
      if (event.data.event_id === 'my_cors_message') {
        console.log(event.data.data);
        if (event.data.data === "exittoscan") {
          this.props.navigateTo("menuScreen")
        }
        if (event.data.data === "save") {
          this.props.navigateTo("fileBrowser")
        }
        if (event.data.data === "exittomenu") {
          this.props.navigateTo("menuScreen")
        }
        if (event.data.data === "exittomenufile") {
          this.props.navigateTo("fileBrowser")
        }
      }
    });
  }


  handleKeyDown = (socketData) => {
    if (socketData.type !== 'button') { return }
    let tempCursorX = this.state.cursorX
    let tempCursorY = this.state.cursorY
    let possibleDiagonalJump = false
    switch (socketData.payload) {
      case 'down':
        tempCursorY++
        break
      case 'up':
        tempCursorY--
        break
      case 'ok':
        // //Seçilen düğmenin hedefine gidiyoruz
        // if (this.buttons[this.state.cursorY].target) {
        //   this.props.navigateTo(this.buttons[this.state.cursorY].target)
        // }
        // else {
        //   this.props.groundNavigateTo("ScanProperties")
        //   return
        // }
        break
      case 'back':
        // this.props.navigateTo("fileBrowser")
        return
      default:
        break
    }

    const currentColumnSize = this.buttons.length

    if (tempCursorY < 0) {
      const lastButtonOnColumn = currentColumnSize - 1
      tempCursorY = lastButtonOnColumn
    } else if (tempCursorY > currentColumnSize - 1) {
      tempCursorY = 0
    }

    const currentRowSize = this.buttons[tempCursorY].length

    if (possibleDiagonalJump) {
      if (tempCursorX > currentRowSize - 1) {
        let lastElementOnRow = currentRowSize - 1
        tempCursorX = lastElementOnRow
      }
    } else {
      if (tempCursorX < 0) {
        const lastButtonOnRow = currentRowSize - 1
        tempCursorX = lastButtonOnRow
      } else if (tempCursorX > currentRowSize - 1) {
        tempCursorX = 0
      }
    }

    this.setState({
      cursorX: tempCursorX,
      cursorY: tempCursorY,
      selectedButtonName: this.buttons[tempCursorY].name
    })
  }

  render() {
    return (
      <div className="viewer">
        {this.state.loading ? (
          <div className="viewer-preloader" ref="preloader">
            <div className="square"></div>
            <div className="preloader-text">Generating 3D Graphic </div>
          </div>
        ) : null}
        <iframe className="threed" title="3d" onLoad={this.hideSpinner} src={"http://localhost:8080/3d-graphic-core/#" + this.props.open  + "#" + window.localStorage.getItem("background_3d")+ "#" + window.localStorage.getItem("language")}></iframe>
        {/* <div className="title"> <i className="fas fa-chart-area"></i> 3D Ground Viewer </div> */}
        {
          console.log()
        }
        {/* <div className="overlay" ref="overlay"></div> */}
        <SocketHelper onMessage={this.handleKeyDown} />
      </div>
    )
  }
}

export default Viewer