import React, { Component } from 'react';
import Moment from 'react-moment';
import 'moment-timezone';
import './StatusBar.css'
import SocketHelper from '../../SocketHelper'
import Battery from './Battery'
import FlashLight from '../../Assets/flashlight.png'
import Volume from '../../Assets/volume.png'
import Wifi from '../../Assets/wifi.png'


class StatusBar extends Component {
  constructor(props) {
    super(props)

    this.needGPS = true;
    this.needTime = true;

    this.state = {
      level: 0,
      isCharging: 'none',
      flashlight: 0,
    }

  }


  componentDidMount() {

    // setInterval(this.getGpsData, 2000)
    // setInterval(this.getTimeData, 3000)
    setInterval(this.getBatteryData, 5000)

  }

  getBatteryData = async () => {
    if(this.props.title == "menuScreen"){
      let sensorResponse = await this.refs.socket.requestScan("P")
      if(sensorResponse.type === 'battery'){
        if((parseInt(sensorResponse.payload)) >= 0 ){
          this.setState({ level: Math.abs(Math.trunc(sensorResponse.payload))})
          this.setState({ isCharging: "none"})
        }else{
          this.setState({ level: Math.abs(Math.trunc(sensorResponse.payload))})
          this.setState({ isCharging: "block"})
        }
      }
    }
  }

  getGpsData = async () => {
    if (this.needGPS) {
      let sensorResponse = await this.refs.socket.requestScan("G")
      console.log(sensorResponse)
      if (sensorResponse.payload.success && typeof sensorResponse.payload.lon != "undefined" && typeof sensorResponse.payload.lat != "undefined") {
        this.needGPS = false;
        this.refs.gps.style.display = "block"

        console.log('%cGPS:' + sensorResponse.payload.lat + " " + sensorResponse.payload.lon, 'background: #000; color: #bada55; font-weight: 600;');
      }
    }
  }

  getTimeData = async () => {
    if (this.needTime) {
      let sensorResponse = await this.refs.socket.requestScan("J")
      if (typeof sensorResponse.payload.minute != "undefined" && sensorResponse.payload.success) {
        console.log('%cDATETIME:' + sensorResponse.payload.hour + ":" + sensorResponse.payload.minute + ":" + sensorResponse.payload.second + " - " + sensorResponse.payload.day + ":" + sensorResponse.payload.month + ":" + sensorResponse.payload.year, 'background: #000; color: #bada55; font-weight: 600');
        this.needTime = false;
        this.refs.time.style.display = "block"
      }
    }
  }

  render() {
    return (
      <div className="status-bar">
        <div className="menu-title">{this.props.strings[this.props.title]}</div>

        <div className="time" style={{ display: "none" }} ref="time">
          <Moment tz={window.localStorage.getItem("timezone")} interval={10000} format="HH:mm">{this.saat}</Moment>
        </div>

        <div className="status-bar-indicator" ref="flashlight" style={{display: (this.state.flashlight<1) ? "none": "block"}} >
          <img src={FlashLight}></img>
        </div>

        <div className="status-bar-indicator">
          <img src={Wifi}></img>
        </div>

        <div className="status-bar-indicator">
          <img src={Volume}></img>
        </div>



        < Battery charging={this.state.isCharging} level={this.state.level}></Battery>
        <i id="gps" ref="gps" style={{ display: 'none' }} className="fas fa-globe-americas"></i>
        <SocketHelper ref='socket'  onMessage={(e) => { if(e.payload == '0' || e.payload == '1' || e.payload == '2'){this.setState({flashlight: parseInt(e.payload)})} return }} />
      </div>
    );
  }
}

export default StatusBar;
